import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import frontend from '../actions/givers';
import { Box } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import LoadingBtn from '../components/CustomButtons/LoadingButton';

export default function ResponsiveDialog(props) {
  //const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  
  const [selectedFile, setSelectedFile] = useState<Number[]>([]);
  const [selectedFileName, setSelectedFileName] = useState("Nenhum Ficheiro Selecionado");
  
  const [loading, setLoading] = useState(false);
  
  const fileHandler = (event) => {
    event.target.files[0].arrayBuffer().then(function (result){
      setSelectedFile(Array.from(new Int8Array(result)));
    });
    setSelectedFileName(event.target.files[0].name);
  };
  
  const editProfile = () => {
    if(props.profilePic){
      setLoading(true)
      frontend.editProfilePhoto(selectedFile).then((response) => {
        setLoading(false)
        enqueueSnackbar(response.text, { variant: 'success' });
        props.setPhoto(response.link)
        handleClose();
      }).catch((error)=>{
        setLoading(false)
        enqueueSnackbar(error.message, { variant: 'error' });
      });
    }else{
      props.setPhoto(selectedFile)
      handleClose();
    }
  }

  const handleClose = () => {
    props.setOpen(false);
  };

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={props.open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{props.profilePic?"Escolher Nova Foto de Perfil":"Escolher Foto"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Imagem selecionada: <br/> 
            <Box textAlign = "center">
              {selectedFileName}
            </Box> 
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <input
            accept="image/*"
            style={{ display: 'none' }}
            id="raised-button-file"
            type="file"
            onChange={fileHandler}
          />

          <label htmlFor="raised-button-file">
            <Button component="span" color="primary" disabled={loading}>
              Escolher Imagem
            </Button>
          </label>
          <Button onClick={editProfile} color="primary" variant="contained" disabled={selectedFile.length==0 || loading}>
            <LoadingBtn text={props.profilePic?"Submeter":"Escolher"} loading={loading}/>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}