import React, { useState } from 'react';
import { useHistory, Redirect, useLocation } from 'react-router-dom';
import frontend from '../actions/givers';

import NavBar from '../navbar';
import Item from '../components/itemCard';
import { Grid, Box } from '@material-ui/core';

function Verify() {
  var location = useLocation();
  var history = useHistory();
  var params = new URLSearchParams(location.search);
  console.log("path: "+location.pathname);
  var [textD,setTextD] = React.useState("")

  React.useEffect(()=>{
    
    frontend.VerifyAccount(params.get('u'),params.get('c')).then((text)=>{
      setTextD(text);
      setTimeout(()=>{
        history.push('/')
      },5000)
    }).catch((e)=>{
      setTextD(e.message);
    })
  },[])
    
  
  

  return (
    <Box width="100%" height="100%" display="flex" justifyContent="center" alignContent="center">
        <p>{textD}</p>
    </Box>
  );
}

export default Verify;