import React, {ChangeEvent} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper, { PaperProps } from '@material-ui/core/Paper';
import { MenuItem, TextField } from '@material-ui/core';
import frontend from '../../actions/givers';
import { useSnackbar } from 'notistack';
import LoadingBtn from '../CustomButtons/LoadingButton'

function PaperComponent(props: PaperProps) {
  return (
      <Paper {...props} />
  );
}

export default function DraggableDialog(props) {
    const [type, setType] = React.useState("");
    const [title, setTitle] = React.useState("");
    const [text, setText] = React.useState("");
    const [loading, setLoading] = React.useState(false);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function typeHandler(e:ChangeEvent<HTMLInputElement>){
    setType(e.target.value);
  }
  
  function titleHandler(e:ChangeEvent<HTMLInputElement>){
    setTitle(e.target.value);
  }

  function textHandler(e:ChangeEvent<HTMLInputElement>){
    setText(e.target.value);
  }
  


  const handleClose = () => {
    props.setOpen(false);
  };
  
  const handleSubmit = () => {
    setLoading(true)
    frontend.sendReport(type,title,text).then((text)=> {
      setLoading(false)
      enqueueSnackbar(text, { variant: 'success' });
    }).catch((error)=>{
      setLoading(false)
      enqueueSnackbar(error.message, { variant: 'error' });
    });
    handleClose();
  };


  const handleCancel = () => {
    handleClose();
  };

  const typeList = [
    {
      value: 'suggestion',
      label: 'Sugesto',
    },
    {
      value: 'report',
      label: 'Denncia',
    },
    {
      value: 'bug',
      label: 'Bug',
    },
  ]

  return (
    <div>
    
      <Dialog
        open={props.open}
        onClose={handleClose}
        PaperComponent={PaperComponent}
      >
        <DialogTitle style={{ cursor: 'default' }} id="draggable-dialog-title">
          Enviar um Ticket
        </DialogTitle>
        <DialogContent>
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="type"
            label="Categoria"
            value={type}
            select
            onChange={typeHandler}
          >
           {typeList.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="title"
            label="Ttulo"
            value={title}
            onChange={titleHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="text"
            label="Texto"
            multiline
            rows={10}
            maxRows={10}
            value={text}
            onChange={textHandler}
            error={text.length==300}
            helperText={text.length==300 ? 'Mximo 300 caracteres 300/300' : ''}
            inputProps={{ maxLength: 300 }} 
          />
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleCancel} color="primary">
            Cancelar
          </Button>
          <Button variant="contained" onClick={handleSubmit} disabled={loading} color="primary">
            <LoadingBtn loading={loading} text={"Enviar"}/> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}