import { userInfo } from 'os';
import React, { useEffect, useState } from 'react';
import { useHistory, Redirect } from 'react-router-dom';
import frontend from '../actions/givers';
import { createStyles, makeStyles, Grid, Paper, Divider, Card, Avatar, Box, CircularProgress } from '@material-ui/core';
import Top from '../components/topCard';
import  '../CustomStyle.css';

interface UserInfo {
    eventsJoined: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: [
            {
                valueType: string,
                excludeFromIndexes: boolean,
                meaning: number,
                value: string
            },
            {
                valueType: string,
                excludeFromIndexes: boolean,
                meaning: number,
                value: string
            }
        ]
    },
    eventsParticipated: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: []
    },
    groupsJoined: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: []
    },
    inEvent: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: boolean
    },
    name: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string
    },
    role: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string
    },
    scorePoints: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: number
    },
    state: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: boolean
    },
    username: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string
    },
    "photo": {
        "valueType": string,
        "excludeFromIndexes": boolean,
        "meaning": number,
        "value": string
    }

}


function Profile() {

    const [topusers, setTopUsers] = useState<UserInfo[]>([]);
    const [box, setBox] = useState("flex");
    
    const titleStyle={
        width: '34ch',
          borderRight:'3px solid',
          fontSize: '2em',
          fontWeight: '700',
          fontFamily: 'FreeMono, monospace'	,
          color:'#7d7f86',
        }

 useEffect(() => {
    frontend.getTopUsers().then( (users) => {
        setTopUsers(users);
        setBox("none")
    });
  }, []);



  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }
   
  return (
    <>
    
    <p style={{width: '34ch',
          borderRight:'1px ',
          borderTop:'1px',
          fontSize: '3em',
          fontWeight: 'bold',
          padding:'none',
          fontFamily: 'FreeMono, monospace'	,
          color:'#57A0D3',
        }}> TOP GIVERS </p>
        <div className="top">
    <div className="typing-demo" color="#5680E9">
      TU podes ser o prximo Top Giver
    </div>
</div>
      <Grid container>
        <Grid item xs={12} sm={12} md={6}>
            {topusers.map(function(u,index){
                return (
                    <div key={index}>
                    <Top position={index+1} username={u.username.value} points={u.scorePoints.value} photo={u.photo.value} key={index}/>
                    <br/>
                    </div>
                    )
                })}
            <Box width="100%" display={box} justifyContent="center" alignItems="center">
                <CircularProgress size={50} variant="indeterminate" color={"inherit"}/>
            </Box>
        </Grid>
        <Grid item xs={12} sm={12} md={6}></Grid>
        
      </Grid>
      
    </>
  );
}

export default Profile;