import React, { ChangeEvent } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { CircularProgress, Link } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory, useLocation } from 'react-router';
import frontend from '../actions/givers';
import Copyright from '../copyright';
import { useSnackbar } from 'notistack';


const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    backgroundImage: 'url(https://source.unsplash.com/random)',
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(0, 5),
    height: `100%`,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));


function ForgotPw() {
  var location = useLocation();
  const classes = useStyles();
  var history = useHistory();
  var params = new URLSearchParams(location.search);
  var [username,setUsername] = React.useState("")
  var [email,setEmail] = React.useState("")
  var [pass,setPass] = React.useState("")
  var [passConfirm,setPassConfirm] = React.useState("")
  const [loading, setLoading] = React.useState(false);
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function ResetPassword(){
    setLoading(true)
    frontend.ForgotPassword(params.get('c'),username,email,pass,passConfirm).then((text)=>{
      setLoading(false)
      enqueueSnackbar(text, { variant: 'success' });
      setTimeout(()=>{
        history.push('/')
      },5000)
    }).catch((e)=>{
      setLoading(false)
      enqueueSnackbar(e.message, { variant: 'error' });
    })
  }
    
  function usernameHandler(e:ChangeEvent<HTMLInputElement>){
    setUsername(e.target.value);
  }
    
  function emailHandler(e:ChangeEvent<HTMLInputElement>){
    setEmail(e.target.value);
  }
    
  function passHandler(e:ChangeEvent<HTMLInputElement>){
    setPass(e.target.value);
  }
    
  function passConfirmHandler(e:ChangeEvent<HTMLInputElement>){
    setPassConfirm(e.target.value);
  }
  
  function LoadingBtn(){
    if(!loading){
      return <>Redefinir Palavra Passe</>;
    }else
    return <CircularProgress size={24} variant="indeterminate" color={"inherit"}/>;
  }

  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      <Grid item xs={false} sm={4} md={7} className={classes.image} />
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Restaurar Palavra Passe
          </Typography>
          <form className={classes.form} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="username"
              label="Username"
              name="username"
              autoComplete="username"
              autoFocus
              value={username}
              onChange={usernameHandler}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email"
              name="email"
              autoComplete="email"
              value={email}
              onChange={emailHandler}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="password"
              label="Password"
              name="password"
              type="password"
              autoComplete="password"
              
              value={pass}
              onChange={passHandler}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Confrimao Password"
              type="password"
              id="password"
              autoComplete="current-password"
              value={passConfirm}
              onChange={passConfirmHandler}
            />
            <Button
              onClick={ResetPassword}
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              <LoadingBtn/>
            </Button>
            <Grid container>
              <Grid item xs>
                
              </Grid>
              <Grid item>
                <Link onClick={()=>{history.push('/')}} variant="body2" style={{cursor: "pointer"}}>
                  {" Voltar para a Pgina Principal"}
                </Link>
              </Grid>
            </Grid>
            <Box mt={5}>
              <Copyright />
            </Box>
          </form>
        </div>
      </Grid>
    </Grid>
  );
}

export default ForgotPw;