import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import { Badge, Box, Grid, TextField } from '@material-ui/core';
import AddPhotoDialog from '../../components/editProfilePic';

const useStyles = makeStyles((theme)=>({
    root: {
      maxWidth: 345,
      margin: theme.spacing(3, 1, 2),
    },
    actions: {
        justifyContent: "flex-end",
    },

  }));

function Top(props){
    const classes = useStyles();
    const buyBtn = React.useState("Editar")[0];

    const name = React.useState(props.item.name.value)[0];
    const [quantity,setQuantity] = React.useState(props.item.quantity.value);
    const [description,setDescription] = React.useState(props.item.description.value);
    const [price,setPrice] = React.useState(props.item.pricePer.value);
    const photo = React.useState(props.item.photo?props.item.photo.value:"")[0];
    const [photoArray,setPhotoArray] = React.useState([]);

    const [photoDialogOpen,setPhotoDialogOpen] = React.useState(false);

    function handleDescription(e:React.ChangeEvent<HTMLInputElement>){
        setDescription(e.target.value);
    }
    const handlePrice = (e:React.ChangeEvent<HTMLInputElement>) => {
        setPrice(Math.max(1,parseInt(e.target.value)));
    };
    const handleQuantity = (e:React.ChangeEvent<HTMLInputElement>) => {
        setQuantity(Math.max(0,parseInt(e.target.value)));
    };

    function DeleteItem(){
        props.delete(name,props.index)
    }

    return(
        <Card className={classes.root}>
            <AddPhotoDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhotoArray} profilePic={false}/>
           
            <CardActionArea onClick={()=>{setPhotoDialogOpen(true)}}>
                <CardMedia
                component="img"
                alt="Item image"
                height="140"
                image={photo!==""?photo:"https://monzta.net/img/homeMarket.png"}
                title="Item image"
                />
                </CardActionArea>
                <CardContent>
                <Grid container spacing={2} style={{width:"100%"}}>
                    <Grid item sm={12} xl={12}>
                        <TextField
                            id={"titulo"+props.index}
                            label="Titulo"
                            variant="outlined"
                            value={name}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item sm={12} xl={12}>
                        <TextField
                            id={"desc"+props.index}
                            label="Descrio"
                            variant="outlined"
                            value={description}
                            onChange={handleDescription}
                            multiline
                            rows={2}
                            fullWidth
                        />
                    </Grid>
                </Grid>
                </CardContent>
            <CardActions className={classes.actions}>
                <Box width={"100%"} height="100%" display="flex" justifyContent="center" alignItems="center">
                <Grid container justifyContent="space-around" spacing={1}>
                    <Grid item sm={12} xl={4}>

                    <TextField
                        id={"cost"+props.index}
                        label="Custo"
                        variant="outlined"
                        value={price}
                        type="number"
                        onChange={handlePrice}
                        fullWidth
                        />
                    </Grid>

                    <Grid item sm={12} xl={4}>
                        <TextField
                            id={"quantity"+props.index}
                            label="Quantidade"
                            variant="outlined"
                            value={quantity}
                            type="number"
                            fullWidth
                            onChange={handleQuantity}
                        />
                    </Grid>
                    <Grid item container sm={12} xl={4} spacing={1}>
                        <Grid item xl={12}>
                            <Badge anchorOrigin={{vertical: 'top', horizontal: 'left'}} invisible={photoArray.length===0} color="secondary" badgeContent={""} showZero style={{width:"100%"}}>
                            <Button size="small" variant="contained" color="primary" onClick={()=>{props.edit(name, description, price, quantity,photoArray)}} fullWidth>
                                {buyBtn}
                            </Button>
                            </Badge>
                        </Grid>
                        <Grid item xl={12}>
                            <Button size="small" variant="contained" onClick={DeleteItem} fullWidth>
                                {"Eliminar"}
                            </Button>
                        </Grid>
                    </Grid>               
                </Grid>
                </Box>
            </CardActions>
        </Card>
    );
}

export default Top;