import React, {ChangeEvent} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper, { PaperProps } from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import { Badge, Grid, TextField } from '@material-ui/core';
import LoadingBtn from '../CustomButtons/LoadingButton';
import frontend from '../../actions/givers';
import { useSnackbar } from 'notistack';

import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';

import AddPhotoDialog from '../../components/editProfilePic';

import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import { useHistory } from 'react-router';
function PaperComponent(props: PaperProps) {
  return (
      <Paper {...props} />
  );
}

export default function DraggableDialog(props) {
  const history = useHistory();
    const [address, setAddress] = React.useState(props.inst?props.inst.address.value:"");
    const [name, setName] = React.useState(props.inst?props.inst.name.value:"");
    const [email, setEmail] = React.useState(props.inst?props.inst.email.value:"");
    const [phone, setPhone] = React.useState(props.inst?props.inst.phoneNr.value:"");
    
    const [photo, setPhoto] = React.useState([]);
    const [photoDialogOpen, setPhotoDialogOpen] = React.useState(false);
    
    const [loading, setLoading] = React.useState(false);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function addressHandler(e:ChangeEvent<HTMLInputElement>){
    setAddress(e.target.value);
  }
  
  function phoneHandler(e:ChangeEvent<HTMLInputElement>){
    setPhone(e.target.value);
  }

  function nameHandler(e:ChangeEvent<HTMLInputElement>){
    setName(e.target.value);
  }
  
  function emailHandler(e:ChangeEvent<HTMLInputElement>){
    setEmail(e.target.value);
  }

  const handleClose = () => {
    props.setOpen(false);
  };
  
  const handleSubmit = () => {
    setLoading(true)
    frontend.registInst(name,email,phone,address,photo).then((text)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar("Instituio Criada Com Sucesso", { variant: 'success' });
      window.location.reload();
    }).catch((error)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };

  const handleEdit = () => {
    setLoading(true)
    frontend.editInst(address,email,phone,photo).then((text)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar(text, { variant: 'success' });
    }).catch((error)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };

  const handleCancel = () => {
    handleClose();
  };
  return (
    <div>
    
      <Dialog
        open={props.open}
        onClose={handleClose}
        PaperComponent={PaperComponent}
      >
        <AddPhotoDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhoto} profilePic={false}/>
        <DialogTitle style={{ cursor: 'default' }} id="draggable-dialog-title">
          Criar Instituio
        </DialogTitle>
        <DialogContent>
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="name"
            label="Nome"
            name="name"
            value={name}
            onChange={nameHandler}
            disabled={props.inst}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="address"
            label="Morada"
            name="adress"
            value={address}
            onChange={addressHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="email"
            label="Email"
            name="email"
            value={email}
            onChange={emailHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="phone"
            label="Telemvel"
            name="phone"
            value={phone}
            onChange={phoneHandler}
          />
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleCancel} color="primary">
            Cancelar
          </Button>
          <Badge invisible={photo.length==0} color="secondary" badgeContent={""} showZero>
            <Button variant="contained" onClick={()=>setPhotoDialogOpen(true)} color="primary" disabled={loading}>
              <AddAPhotoIcon/>
            </Button>
          </Badge>
          <Button variant="contained" onClick={props.inst?handleEdit:handleSubmit} color="primary" disabled={loading}>
            <LoadingBtn text={props.inst?"Editar":"Criar"} loading={loading}/>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}