import React, { useState, ChangeEvent } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import frontend from '../../actions/givers';
import { Box, TextField, Grid } from '@material-ui/core';
import { useSnackbar } from 'notistack';
export default function ResponsiveDialog(props) {
  //const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  

  function passHandler(e:ChangeEvent<HTMLInputElement>){
    setPassword(e.target.value);
  }

  function passConfirmHandler(e:ChangeEvent<HTMLInputElement>){
    setPasswordConfirm(e.target.value);
  }

  const deleteAccount = () => {
    frontend.deleteUser(password, passwordConfirm).then((text) => {
      enqueueSnackbar(text, { variant: 'success' });
      handleClose();
    }).catch((error)=>{
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  }

  const handleClose = () => {
    props.setOpen(false);

  };

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={props.open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Apagar Conta"}</DialogTitle>
        <DialogContentText>
         Tem a certeza que quer apagar a sua conta?
          
        </DialogContentText>

        <DialogContent>
          <Grid container  spacing={2}>
            <Grid item xs={6} sm={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Password"
                type="password"
                value={password}
                onChange={passHandler}
              />
            </Grid>
            <Grid item xs={6} sm={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Password (Confirmao)"
                type="password"
                value={passwordConfirm}
                onChange={passConfirmHandler}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <label htmlFor="raised-button-file">
            <Button onClick={handleClose} color="primary" style={{backgroundColor: '#84CEEB', color: 'white'}}>
              Cancelar
            </Button>
          </label>
          <Button onClick={deleteAccount} style={{backgroundColor: '#84CEEB', color: 'white'}}>
            Apagar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

