import React, { ChangeEvent, useEffect, useState } from 'react';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import InputLabel from '@material-ui/core/InputLabel';
// core components
import GridItem from '../components/Grid/GridItem';
import GridContainer from '../components/Grid/GridContainer';
import CustomInput from '../components/CustomInput/CustomInput';
//import Button from '../components/CustomButtons/Button';
//import Card from '../../components/Card/Card';
import CardHeader from '../components/Card/CardHeader';
import CardAvatar from '../components/Card/CardAvatar';
import CardBody from '../components/Card/CardBody';
import CardFooter from '../components/Card/CardFooter';
import avatar from '../assets/img/faces/default.png';
import { createStyles, makeStyles, Grid, CardMedia, Paper, Divider, List, Avatar, Box, TextField,  Button, Dialog, Card, CardContent, CardActions,  IconButton, Typography, Collapse, DialogActions, DialogContent, DialogContentText, DialogTitle, MenuItem, Backdrop, CircularProgress, CardActionArea, Badge} from '@material-ui/core';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  Redirect,
  useHistory
} from "react-router-dom";
import ListItem, { ListItemProps } from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { useSnackbar } from 'notistack';

import CommentIcon from '@material-ui/icons/Comment';
import CommentDialog from '../components/Forms/CommentDialog';

import ProfilePicDialog from '../components/editProfilePic';
import PasswordDialog from '../components/Forms/changePassDialog';
import DeleteAccDialog from '../components/Forms/deleteAccountForm';

import frontend from '../actions/givers';

import Map from '../components/map';
import InstCard from '../components/instCard';

import Comment from '../components/commentCard';
import CommentsList from '../components/commentsList';

import LoadingBtn from '../components/CustomButtons/LoadingButton';

const styles = createStyles({
  cardCategoryWhite: {
    color: 'rgba(255,255,255,.62)',
    margin: '0',
    fontSize: '14px',
    marginTop: '0',
    marginBottom: '0'
  },
  cardTitleWhite: {
    color: '#FFFFFF',
    marginTop: '0px',
    minHeight: 'auto',
    fontWeight: 300,
    fontFamily: '\'Roboto\', \'Helvetica\', \'Arial\', sans-serif',
    marginBottom: '3px',
    textDecoration: 'none'
  }
});

const useStyles = makeStyles((theme) => ({
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  media: {
    height: 0,
    paddingTop: '18.75%', // 16:9
  },
  large: {
    marginTop:`5%`,
    width: theme.spacing(17),
    height: theme.spacing(17),
    boxShadow: theme.shadows[3],
    cursor: "pointer",
  },
  card: {
    borderRadius: 10,
    height: "100%"
  },
}));


function EventPage(props: any) {
  const { classes } = props;
  const classes2 = useStyles();
  let red = '#FF0000';
  const today = new Date();
  const history = useHistory()
  
  const [passwordDialogOpen,setPasswordDialogOpen] = useState(false);
  const [deleteAccDialogOpen,setDeleteAccDialogOpen] = useState(false);
  const [joined,setJoined] = useState(false);
  const [joining,setJoining] = useState(false);

  const [address,setAddress] = useState<string>("");
  const [capacity,setCapacity] = useState<string>("0");
  const [dateStart,setDateStart] = useState<String>("");
  const [timeStart,setTimeStart] = useState<String>("");
  const [duration,setDuration] = useState<String>("");
  const [dateStartDate,setDateStartDate] = useState<Date>(new Date(0));
  const [dateEndDate,setDateEndDate] = useState<Date>(new Date(0));
  const [desc,setDesc] = useState<string>("");
  const [markers,setMarkers] = useState([]);
  const [walkedMarkers,setWalkedMarkers] = useState([]);
  const [name,setName] = useState<string>("");
  const [comments,setComments] = useState([]);
  const [participants,setParticipants] = useState("");
  const [photo,setPhoto] = useState<string>("https://storage.googleapis.com/givers-volunteering.appspot.com/defaultImages/eventDefault.jpg");
  const [photos,setPhotos] = useState([]);
  const [rating,setRating] = useState();
  const [interest,setInterest] = useState<string>("");
  const [latLng,setLatLng] = useState({lat: 37.772, lng: -122.214});
  
  const [instDialogOpen,setInstDialogOpen] = useState(false);
  const [inst,setInst] = useState({});
  const [nameInst,setNameInst] = useState<string>("");
  
  const [commentsDialogOpen,setCommentsDialogOpen] = useState(false);
  const [writeCommentDialogOpen,setWriteCommentDialogOpen] = useState(false);
  const [commentCursor,setCommentCursor] = useState("");
  const [loadingComments,setLoadingComments] = useState(false);
  
  const [editing,setEditing] = useState(false);
  const [canEdit,setCandEdit] = useState(false);

  const [selectedMarker,setSelectedMarker] = React.useState();
  
  const [loading,setLoading] = React.useState(true);
  
  const [photoArray,setPhotoArray] = useState([]);
  const [photoDialogOpen,setPhotoDialogOpen] = useState(false);

  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function addressHandler(e:ChangeEvent<HTMLInputElement>){
    setAddress(e.target.value);
  }
  function capacityHandler(e:ChangeEvent<HTMLInputElement>){
    setCapacity(e.target.value);
  }
  function dateStartHandler(e:ChangeEvent<HTMLInputElement>){
    setDateStart(e.target.value);
  }
  function timeStartHandler(e:ChangeEvent<HTMLInputElement>){
    setTimeStart(e.target.value);
  }
  function durationHandler(e:ChangeEvent<HTMLInputElement>){
    setDuration(e.target.value);
  }
  function descHandler(e:ChangeEvent<HTMLInputElement>){
    setDesc(e.target.value);
  }
  function interestHandler(e:ChangeEvent<HTMLInputElement>){
    setInterest(e.target.value);
  }

  function nameHandler(e:ChangeEvent<HTMLInputElement>){
    setName(e.target.value);
  }

  const OpenInstCard = () => {
    setInstDialogOpen(true);
  };
  
  const join = () => {
    setJoining(true)
    frontend.joinEventUser(name).then((text)=>{
        setJoining(false)
        setJoined(true);
        setParticipants((parseInt(participants)+1)+"")
        enqueueSnackbar(text, { variant: 'success' });
      }).catch((error)=>{
      setJoining(false)
      if(error.code == 406) setParticipants(capacity)
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };
  
  useEffect(() => { 
    if(!frontend.Authenticated()){
      <Redirect to="/"/>
    }
  }, []);
  
  useEffect(() => {
    if(window.location.pathname.slice(11).replaceAll('%20'," ")!=""){
      frontend.getEvent(decodeURI(window.location.pathname.slice(11))).then((event) => {
        if(event){
          setLoading(false)
          setName(event.name.value);
          setAddress(event.address.value);
          setDesc(event.description.value);
          
          frontend.getInstitution(event.institution.value).then((institution)=>{
            setInst(institution);
            setNameInst(institution.name.value);
            setCandEdit(frontend.getUsername()==institution.owner.value && (today.getTime() < dateStartDate.getTime()));
          })
          
          setCapacity(event.capacity.value);
          setParticipants(event.joinedUsersCount.value);

          if(event.inEvent.value) setJoined(true)
  
          setPhotos(event.photos.value)
          setRating(event.actualRating.value);
          setInterest(event.interests.value.toUpperCase());
          let dateStartDate = new Date(event.date_start.value);
          setDateStartDate(dateStartDate)
          setDateStart(dateStartDate.getFullYear()+"-"+("0" + (dateStartDate.getMonth() + 1)).slice(-2)+"-"+("0" + dateStartDate.getDate()).slice(-2));
          setTimeStart(("0" + dateStartDate.getHours()).slice(-2)+":"+("0" + dateStartDate.getMinutes()).slice(-2))
          let duration = (event.date_end.value-event.date_start.value);
          setDateEndDate(new Date(event.date_end.value));
          setDuration(("0" +Math.floor((duration / (1000 * 60 * 60)) % 24)).slice(-2)+":"+("0" +Math.floor((duration / (1000 * 60)) % 60)).slice(-2))
          setPhoto(event.photo.value);
          setLatLng({lat: event.lat.value, lng: event.lon.value},);
          
          frontend.getMarkers(event.name.value).then((markersList)=>{
            setMarkers(markersList);
          })
          frontend.getMarkersTracking(event.name.value).then((markersList)=>{
            setWalkedMarkers(markersList);
          }).catch((error)=>{
          })
        }
      }).catch((error)=>{
        enqueueSnackbar(error.message, { variant: 'error' });
        if(error.code == 404) history.push('/app/dashboard')
        
      });
    }
  }, []);
  
  const edit = () => {
    setEditing(!editing);
    if(editing){
      frontend.editEvent(address,capacity,desc,duration,interest,new Date(dateStart+"T"+timeStart).getTime(),name,markers,photoArray).then((text)=>{
        enqueueSnackbar(text, { variant: 'success' });
      }).catch((error)=>{
        enqueueSnackbar(error.message, { variant: 'error' });
      })
    }
  };

  function InstDialog(){
    return(
      <>
      <Dialog
        fullScreen={false}
        scroll={'paper'}
        open={instDialogOpen}
        onClose={()=>{setInstDialogOpen(false)}}
        aria-labelledby="responsive-dialog-title"
      >
        <InstCard inst={inst}/>
      </Dialog>
      </>
    );
  }

  function openComments(){
    if(comments.length==0 && commentCursor=="" && !loadingComments){
    setLoadingComments(true);
    frontend.getComments(name,commentCursor,true).then((commentList)=>{
       setLoadingComments(false);
        setComments(commentList.list);
        setCommentCursor(commentList.cursor);
        setCommentsDialogOpen(true);
      });
    }else{
      setCommentsDialogOpen(true);
    }
  }

  function BtnTextInst(){
    if(today.getTime() > dateStartDate.getTime()) {
      if(today.getTime() < dateEndDate.getTime()) return <>Decorrente</>
      return <>Terminado</>
    }
    if(!editing) return <>Editar</>;
    
    return <>Submeter</>
  }

  function BtnTextUser(){
    if(joined) return <>Inscrito</>;

    if(today.getTime() > dateEndDate.getTime()) {
      //if(today.getTime() < dateEndDate.getTime()) return <>Decorrente</>
      return <>Terminado</>
    }

    if(parseInt(capacity)==parseInt(participants)) return <>Cheio</>;

    return <>Juntar</>
  }

  function ActionBtn(){
    if(frontend.isInst()) return(
      <Badge anchorOrigin={{vertical: 'top', horizontal: 'right'}} invisible={photoArray.length==0 || !editing} color="secondary" badgeContent={""} showZero>
        <Button color="primary" variant="contained" onClick={edit} disabled={!canEdit}><BtnTextInst/></Button>
      </Badge>
    );
    return(
      <Button color="primary" variant="contained" onClick={join} disabled={parseInt(capacity)==parseInt(participants) || joined || joining ||  (today.getTime() > dateEndDate.getTime())}><LoadingBtn loading={joining} text={<BtnTextUser/>}/></Button>
    );
  }
  

  function CommentsDialog(){
    return(
      <>
      <ProfilePicDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhotoArray} profilePic={false}/>
      <CommentDialog open={writeCommentDialogOpen} setOpen={setWriteCommentDialogOpen} name={name}/>
      <Dialog
        fullWidth
        maxWidth={'lg'}
        open={commentsDialogOpen}
        onClose={()=>{setCommentsDialogOpen(false)}}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="scroll-dialog-title">Comentrios</DialogTitle>
        <DialogContent dividers>
         
          <CommentsList name={name} comments={comments} cursor={commentCursor}/>
        </DialogContent>
        <DialogActions>
          <Button color="primary" variant="contained" onClick={()=>{setCommentsDialogOpen(false);setWriteCommentDialogOpen(true)}}>
            Comentar
          </Button>
          <Button color="primary" variant="contained" onClick={()=>{setCommentsDialogOpen(false)}}>
            Fechar
          </Button>
        </DialogActions>
      </Dialog>
      </>
    );
  }

  return (
    <div style={{marginTop:`0%`}}>
      <InstDialog />
      <CommentsDialog/>
      <Backdrop className={classes.backdrop} open={loading} style={{zIndex:100}}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <PasswordDialog open={passwordDialogOpen} setOpen={setPasswordDialogOpen}/>
      <DeleteAccDialog open={deleteAccDialogOpen} setOpen={setDeleteAccDialogOpen}/>
      <Grid container direction="column" spacing={1}>
        <Grid item container spacing={3} >
        <Grid container item xs={12} sm={12} md={6} lg={5} direction="column" spacing={2}>
          <Grid item>
            <Card className={classes2.card}>
                <Box display="flex" justifyContent="center" alignItems="center" maxHeight={'100%'} overflow="hidden">

                  <CardActionArea disabled={!editing} onClick={()=>{setPhotoDialogOpen(true)}}>
                  <CardMedia
                    component="img"
                    alt="Evento Fofinho :)"
                    className={classes.media}
                    image={photo}
                    title={name}
                    />
                  </CardActionArea>
                </Box>
                <Box padding={1} paddingBottom={0} paddingTop={0} textAlign="center">
                  <Grid container></Grid>
                  <h2 className={classes.cardTitle}>{name}</h2>
                    
                    <Divider/>
                    <Grid container>
                      <Grid item xs={4}>
                        <h3>
                          {participants+"/"+capacity} <br />
                          <small>Participantes</small>
                    
                        </h3>
                      </Grid>
                      <Grid item xs={4}>
                        <h3>
                          {parseFloat(rating+"").toFixed(1) + "/5.0 "} <br />
                          <small>Classificao</small>
                        </h3>
                      </Grid>
                      <Grid item xs={4}>
                        <Box width="100%" height="100%" display="flex" alignItems="center" justifyContent="center">
                          <ActionBtn/>
                        </Box>
                      </Grid>
                    </Grid>
                </Box>
              </Card>
          </Grid>
          <Grid item container spacing={2}>
            <Grid item xs={12} sm={9} md={12} lg={9} xl={10}>
              <Paper className={classes2.card}>
                <List component="nav" aria-label="main mailbox folders">
                <ListItem button onClick={OpenInstCard} style={{overflow:"hidden", textOverflow: "ellipsis" }}>
                  <ListItemIcon > 
                    <Avatar style={{ backgroundColor: "#0060ff" }}>
                      {nameInst.slice(0)[0]}
                      
                    </Avatar>
                  </ListItemIcon>
                  <ListItemText primary={nameInst} />
                </ListItem>
                </List>
              </Paper>
            </Grid>
            <Grid item xs={12} sm={3} md={12} lg={3} xl={2}  >
              
              <Paper className={classes2.card} style={{height:'100%'}}>
                <Button style={{height: '100%',width:'100%'}} onClick={openComments} disabled={loadingComments}>
                    <LoadingBtn loading={loadingComments} text={<CommentIcon fontSize="large" color="action"/>}/>
                </Button>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={6} lg={7}>
          <Card className={classes2.card}>
            <Map walkedMarkers={walkedMarkers} selectedMarker={selectedMarker} setSelectedMarker={setSelectedMarker} markers={markers} setMarkers={setMarkers} center={latLng} editable={editing} mapEvents={[]} zoom={15} onLoad={()=>{}}/>
            
          </Card>
        </Grid>
     </Grid>
        <Grid item> 
          <Paper className={classes2.card}>
              <Box padding={2} paddingTop={3}>
                <Grid container spacing={2}>
                  
                    <Grid item  xs={12} sm={12} md={4}>
                      <TextField
                      variant="outlined"
                      id="name"
                      label="Nome"
                      value={name}
                      disabled
                      fullWidth
                    />
                    </Grid>
                    <Grid item  xs={12} sm={12} md={4}>
                      <TextField
                      variant="outlined"
                      label="Morada"
                      id="address"
                      value={address}
                      onChange={addressHandler}
                      fullWidth
                      disabled={!editing}
                    />
                    </Grid>

                    <Grid item  xs={12} sm={12} md={4}>
                      <TextField
                        variant="outlined"
                        label="Interesse"
                        id="interest"
                        value={interest}
                        onChange={interestHandler}
                        disabled={!editing}
                        fullWidth
                        select
                      >
                        {frontend.getInterests().map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                      </TextField>
                    </Grid>



                    <Grid item  xs={12} sm={12} md={8}>
                      <TextField
                          variant="outlined"
                          id="desc"
                          label="Descrio"
                          value={desc}
                          onChange={descHandler}
                          multiline
                          rows={5}
                          rowsMax={5}
                          fullWidth
                          disabled={!editing}
                          error={desc.length==300}
                          helperText={desc.length==300 ? 'Mximo 300 caracteres 300/300' : ''}
                          inputProps={{ maxLength: 300 }} 
                        />
                    </Grid>

                    <Grid container item  xs={12} sm={12} md={4} spacing={3}>
                      <Grid item  xs={12} sm={12} md={12}>
                        <TextField
                          variant="outlined"
                          label="Data de Inicio"
                          id="dateStart"
                          type="date"
                          value={dateStart}
                          onChange={dateStartHandler}
                          InputLabelProps={{ shrink: true }}
                          disabled={!editing}
                          fullWidth
                        />
                      </Grid>
                      <Grid item  xs={12} sm={12} md={6}>
                        <TextField
                          variant="outlined"
                          label="Hora de Inicio"
                          id="timeStart"
                          type="time"
                          value={timeStart}
                          onChange={timeStartHandler}
                          InputLabelProps={{ shrink: true }}
                          fullWidth
                          disabled={!editing}
                        />
                      </Grid>
                      <Grid item  xs={12} sm={12} md={6}>
                        <TextField
                          variant="outlined"
                          label="Duro (HH:MM)"
                          id="duration"
                          type="time"
                          value={duration}
                          onChange={durationHandler}
                          InputLabelProps={{ shrink: true }}
                          fullWidth
                          disabled={!editing}
                        />
                      </Grid>
                    </Grid>
                    
          
                  
                
        
                  
                   
                  
                </Grid>
              </Box>
              
            </Paper>
        </Grid>
      </Grid>
    </div>
  );
}

export default withStyles(styles)(EventPage);
