import React, { useState, ChangeEvent } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import frontend from '../../actions/givers';
import { Box, TextField, Grid } from '@material-ui/core';
import { useSnackbar } from 'notistack';

export default function ResponsiveDialog(props) {
  //const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newPasswordConfirm, setNewPasswordConfirm] = useState("");
  

  function oldPassHandler(e:ChangeEvent<HTMLInputElement>){
    setOldPassword(e.target.value);
  }
  function newPassHandler(e:ChangeEvent<HTMLInputElement>){
    setNewPassword(e.target.value);
  }
  function newPassConfirmHandler(e:ChangeEvent<HTMLInputElement>){
    setNewPasswordConfirm(e.target.value);
  }

  const editPassword = () => {
    frontend.editPassword(oldPassword,newPassword,newPasswordConfirm).then((text) => {
      enqueueSnackbar(text, { variant: 'success' });
      handleClose();
    }).catch((error)=>{
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  }

  const handleClose = () => {
    props.setOpen(false);
    setOldPassword("");
    setNewPassword("");
    setNewPasswordConfirm("");
  };

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={props.open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Mudar Palavra Passe"}</DialogTitle>
        <DialogContent>
          <Grid container  spacing={2}>
            <Grid item xs={6} sm={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Password Antiga"
                type="password"
                value={oldPassword}
                onChange={oldPassHandler}
              />
            </Grid>
            <Grid item xs={6} sm={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Password Nova"
                type="password"
                value={newPassword}
                onChange={newPassHandler}
              />
            </Grid>
            <Grid item xs={6} sm={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Password Nova (Confirmao)"
                type="password"
                value={newPasswordConfirm}
                onChange={newPassConfirmHandler}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <label htmlFor="raised-button-file">
            <Button onClick={handleClose} color="primary">
              Cancelar
            </Button>
          </label>
          <Button onClick={editPassword} color="primary">
            Submeter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

