import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Box } from '@material-ui/core';
import frontend from '../actions/givers';
const useStyles = makeStyles((theme)=>({
    root: {
      maxWidth: 345,
      margin: theme.spacing(3, 1, 2),
    },
    actions: {
        justifyContent: "flex-end",
    },

  }));

function Top(props){
    const classes = useStyles();
    const [canBuy,setCanBuy] = React.useState(props.canBuy);
    const [buyBtn,setBuyBtn] = React.useState("Comprar");
    function BuyItem(){
        frontend.buyShopItem(props.title).then(()=>{
            setBuyBtn("Comprado")
            setCanBuy(false)
        }).catch(()=>{
            setBuyBtn("Saldo Insuficiente")
            setCanBuy(false)
        });  
    }

    return(
        <>
        <Card className={classes.root}>
            <CardActionArea>
                <CardMedia
                component="img"
                alt="Item image"
                height="140"
                image={props.image!=""?props.image:"https://monzta.net/img/homeMarket.png"}
                title="Item image"
                />
                <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                    {props.title}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {props.description}
                </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions className={classes.actions}>
                
                <Typography variant="body2" color="textSecondary" component="p" >
                    {props.cost + " Givers "}
                </Typography>
                <Button size="small" variant="contained" color="primary" disabled={!canBuy} onClick={BuyItem}>
                    {buyBtn}
                </Button>
              
            </CardActions>
        </Card>
        </>
    );
}

export default Top;