import React, { useState, useEffect } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import Grid from '@material-ui/core/Grid';
import Institution from './instCard';
import frontend from "../actions/givers";
import Alert from './alert';
import { CircularProgress } from "@material-ui/core";

const style = {
  width: 1000,
  overflow: "hidden",
};
const All = {
  display: "flex",
  justifyContent: "center",
  marginTop:0,
};



function Scroll(){
  const [cursor, setCursor] =useState("");
  const [alert, setAlert] = useState({
    description: "test",
    severity:"warning",
  });
  const [open, setOpen] = useState(false);

  const [items, setItem] = useState([]);

  const [hasMore, setHasMore] = useState(true);


  useEffect(() => {
    // Initialization code comes here
      frontend.listAllInst("").then(function(json) {
        setCursor(json.cursor);
        setItem(items.concat(json.list));
        if(json.cursor=="end") setHasMore(false);
      }); 
  }, []);



  const fetchMoreData = () => {
//    setTimeout(() => {
      frontend.listAllInst(cursor).then(function(json) {
        setCursor(json.cursor);
        setItem(items.concat(json.list));
        if(json.cursor==="end") setHasMore(false);
      });  
//    }, 5000);
    // a fake async api call like which sends
    // 20 more records in .5 secs
  /*  setTimeout(() => {
      setItem(items.concat(Array.from({ length: 21 })));
    }, 1000);*/
  };


  const EndMessage = () => {
    if(items.length == 0){
      return <b>No h instituies para mostrar</b>;
    }
    else{
      return <b>Boa! J viste tudo</b>;
    }
  }

    return (
      <div style={All}>    
        <InfiniteScroll style={style}
          dataLength={items.length}
          next={fetchMoreData}
          hasMore={cursor!="end"}
          loader={<div style={{ textAlign: "center", margin: `3% 0`}}>
          <CircularProgress  variant="indeterminate" color={"primary"}/>
        </div>}
          endMessage={
              <p style={{ textAlign: "center" ,paddingTop: "5px"}}>
              {EndMessage()}
            </p>
          }
          >
          <Grid container spacing={3}>
        
          {items.map((i, index) => (
                <Grid key={index} item sm={12}>
                    <Institution style={style} inst={i} setAlert={setAlert} setOpen={setOpen} />
                </Grid>    
          ))}
          </Grid>
        </InfiniteScroll>
        <Alert description={alert.description} severity={alert.severity} open={open} setOpen={setOpen}/>
        </div>
    );
}

export default Scroll;
 /*<div style={style} key={index}>
              div - #{index}
            </div>*/