import React from 'react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { useEffect } from 'react';

export default function RadioButtonsGroup(props) {
  const [value, setValue] = React.useState("0");

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue((event.target as HTMLInputElement).value);
    };

  useEffect(() =>{
    props.onChangeTimeFilter(+value);
  }, [value]);
    

  return (
    <FormControl component="fieldset">
      <FormLabel component="legend">Eventos</FormLabel>
      <RadioGroup aria-label="time" name="time1" value={value} onChange={handleChange} >
        <FormControlLabel value="-1" control={<Radio />} label="Passados" />
        <FormControlLabel value="1" control={<Radio />} label="Futuros" />
        <FormControlLabel value="0" control={<Radio />} label="Todos" />
      </RadioGroup>
    </FormControl>
  );
}
