import { Paper, Box, Grid, Avatar, Card} from '@material-ui/core';
import React from 'react';
import { makeStyles , createStyles, Theme } from "@material-ui/core/styles";


const useStyles = makeStyles({
    aboutSection: {
      color:'white',
      backgroundColor:'#C1C8E4',
    },
    
  });

function Top(props){
    const classes = useStyles();
    return(
        <>
        <Paper elevation={3} style={{borderRadius:10}}>

            <Grid container spacing={1}>
                
            <Box
                display="flex"
                justifyContent="flex-start"
                m={1}
                p={1}
                bgcolor= {"background.paper"}
                alignItems="center"
            >
                    <Avatar style={{backgroundColor: props.position==1?"#e1b400":props.position==2?"#c8ccc8":props.position==3?"#bb8f4c":"#C5C8E4"}} className={classes.aboutSection}>{props.position}</Avatar>
             </Box>
            
                <Grid  xs={12} sm={12} md={8} >
                    <Box width="50%" height="100%" display="flex" alignItems="center" justifyContent="center">
                        {props.username.charAt(0).toUpperCase() + props.username.slice(1)}
                    </Box>
                </Grid>
             <Grid  justify="flex-end">
                 <Box width="100%" height="100%" display="flex" alignItems="center" justifyContent="center" >

                     <Avatar  src={props.photo} style={{ height: '60px', width: '60px' }} />
                </Box>
            </Grid>
                <Grid item xs={12} sm={12} md={2} justify="flex-start" >
                    <Card style={{height:"100%", borderRadius:10, backgroundColor: '#84CEEB', color: 'white'}}>

                    <Box width="100%" height="100%" display="flex" justifyContent="center" alignItems="center">
                        {props.points} Pontos
                    </Box>
                    </Card>
                    

                </Grid>         
       
            </Grid>
        </Paper>
        </>
    );
}

export default Top;