import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';

import PhotoIcon from '@material-ui/icons/Photo';
import CloseIcon from '@material-ui/icons/Close';

import frontend from '../actions/givers';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
     width: `100%`
    },
    media: {
      height: 0,
      paddingTop: '18.75%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
  }),
);

export default function RecipeReviewCard(props) {
  const classes = useStyles();

  const CloseBtn= () => {
    if(frontend.getUsername()==props.comment.owner.value || frontend.isSuper() || frontend.isBack()){
        return(
            <IconButton aria-label="settings" onClick={()=>{props.remove(props.index,props.comment.commentId.value,props.event)}}>
                <CloseIcon />
            </IconButton>
        );
    }
    return(<></>);
  }

  const PhotoBtn= () => {
    if(props.comment.photoLink.value!=""){
        return(
            <IconButton aria-label="show Photo" onClick={()=>{window.open(props.comment.photoLink.value)}}>
                <PhotoIcon />
            </IconButton>
        );
    }
    return(<></>);
  }

  function PhotoDialog(){

  }


  return (
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar} src={props.comment.profilePhoto?props.comment.profilePhoto.value:""}/>
        }
        action={<CloseBtn/>}
          
        title={props.comment.owner.value}
        subheader={new Date(props.comment.date.value)+""}
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
            {props.comment.comment.value}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
          {<PhotoBtn/>}
      </CardActions>
    </Card>
  );
}