import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import { blue } from '@material-ui/core/colors';
import Share from './share';

const useStyles = makeStyles({

});

function SimpleDialog(props) {
  const classes = useStyles();

  return (
    <Dialog onClose={()=>{props.setOpen(false)}} aria-labelledby="simple-dialog-title" open={props.open} maxWidth={'xs'} fullWidth={false}>
      <DialogTitle id="simple-dialog-title">{"Partilhar "+props.event}</DialogTitle>
      <List>
          <ListItem>
            <Share url={props.event}/>
          </ListItem>
      </List>
    </Dialog>
  );
}

export default function SimpleDialogDemo(props) {
  const [open, setOpen] = [props.open,props.setOpen];
  return (
      <SimpleDialog event={props.event} open={open} setOpen={setOpen} />
  );
}