import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import { Button } from '@material-ui/core';
import EmojiPeopleIcon from '@material-ui/icons/EmojiPeople';
import frontend from '../actions/givers';
import { useHistory } from 'react-router';
import { useSnackbar } from 'notistack';

import Share from './shareDialog';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      
    },
    media: {
      height: 0,
      paddingTop: '18.75%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
  }),
);


export default function RecipeReviewCard(props) {
    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);
    const [kept,setKept] = useState(false);
    const [capacity,setCapacity] = useState(0);
    const [maxCapacity,setMaxCapacity] = useState(0);
    const [joined,setJoined] = useState(false);
    const [openShare, setOpenShare] =useState(false);
    const history = useHistory();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    function KeepButton(){
        if(kept) return  <BookmarkIcon/>;
        return <BookmarkBorderIcon/>;
    }
    
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const keep = () => {
    setKept(!kept);
    frontend.saveEvent(props.event.name.value).then((text)=>{
      enqueueSnackbar(text, { variant: 'success' });
    }).catch((error)=>{
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };
  
    const join = () => {
      setJoined(true);
      frontend.joinEventUser(props.event.name.value).then((text)=>{
        enqueueSnackbar(text, { variant: 'success' });
        setCapacity(capacity+1)
      }).catch((error)=>{
        enqueueSnackbar(error.message, { variant: 'error' });
      });
    };

  function Dur(){
    const duration = Math.floor((props.event.date_end.value - props.event.date_start.value)/60000);
    return <>{duration+" Minutos"}</>;
  }

  function Cap(){
    setMaxCapacity(props.event.capacity.value);
    setCapacity(props.event.participants.value.length);
    return <>{capacity+" / "+maxCapacity}</>;
  }

  return (
    <Card className={classes.root}>
      <Share open={openShare} setOpen={setOpenShare} event={props.event.name.value}/>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            {props.event.institution.value.slice(0)[0].toUpperCase()}
          </Avatar>
        }
        action={
          <IconButton disabled={true} aria-label="settings">
            <div><Dur/>&nbsp;</div>
          <AccessTimeIcon/>
          </IconButton>
        }
        title={props.event.name.value}
        subheader={new Date(props.event.date_start.value).toLocaleString([], { timeZone: 'UTC',year: 'numeric', month: 'numeric', day: 'numeric', hour: '2-digit', minute: '2-digit' })+""}
      />
      
      <CardMedia
        className={classes.media}
        image={props.event.photo.value}
        title={props.event.name.value}
      />
      
      <CardActions disableSpacing>
        <IconButton aria-label="keep" disabled={kept} onClick={keep}>
            <KeepButton/>
        </IconButton>
        <IconButton aria-label="share" onClick={()=>{setOpenShare(true)}}>
          <ShareIcon />
        </IconButton>
        <Button className={classes.expand} variant="contained" onClick={()=>{history.push('event/'+props.event.name.value.replaceAll(' ',""))}}>Saber Mais</Button>
      </CardActions>
    </Card>
  );
}
