import { Box, Button, Card, FormControl, FormControlLabel, Grid, IconButton, InputLabel, MenuItem, Select, Switch, TextField, Tooltip } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react'
import InfoIcon from '@material-ui/icons/Info';
import frontend from '../../actions/giversBO';
import { Redirect } from 'react-router';
import { useSnackbar } from 'notistack';

function AccountRole(){
    const [username,setUsername] = useState("")
    const [role,setRole] = useState("")
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    function handleUsername(e:ChangeEvent<HTMLInputElement>){
        setUsername(e.target.value);
    }

    function handleRole(e:ChangeEvent<HTMLInputElement>){
        setRole(e.target.value);
    }

    const [age, setAge] = React.useState('');
    const handleChange = (event) => {
        setRole(event.target.value);
    };


    function EditRole(){
        frontend.EditRole(username, role).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }

    if(!frontend.isSuper() && !frontend.isBack()){
        return <Redirect to="/app/dashboard"/>
    }

    


    return(
        <>
            <Box height={window.innerHeight-155} display="flex" alignItems="center" justifyContent="center">
                <Card style={{width:"30%"}}>

                    <Grid container justifyContent="space-around" alignItems="center" spacing={2} style={{padding:15}}>
                        
                        <Grid item xl={12}>
                            <TextField
                                id="username"
                                label="Username"
                                variant="outlined"
                                value={username}
                                onChange={handleUsername}
                                fullWidth
                            /> 
                        </Grid>

                        <Grid item xl={12}>
                            <FormControl fullWidth>
                                <TextField
                                    label="Role"
                                    id="roles"
                                    value={role}
                                    onChange={handleChange}
                                    fullWidth
                                    select
                                    variant="outlined"
                                >
                                    <MenuItem value={"USER"}>USER</MenuItem>
                                    <MenuItem value={"INST_OWNER"}>INST_OWNER</MenuItem>
                                    <MenuItem value={"BO"}>BO</MenuItem>
                                </TextField>
                            </FormControl>
                        </Grid>
                        <Grid container item xl={12} direction="row">
                            <Grid item xl={1}>
                                <Tooltip title={"Indique o nome do username da conta que quer alterar o role, e o novo role."}>
                                    <IconButton aria-label="">
                                        <InfoIcon />
                                    </IconButton>
                                </Tooltip>
                            </Grid >
                            <Grid item xl={11}>
                                <Box height="100%" width="100%" display="flex" alignItems="center" justifyContent="flex-end" >
                                    <Button variant="contained" color="primary" onClick={EditRole}>Editar</Button>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                </Card>
            </Box>

     </>
    );

}

export default AccountRole;