import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { Card, CardMedia, CardContent,CardActions,CardActionArea,Paper, Grid, Box } from '@material-ui/core';
import CardHeader from '@material-ui/core/CardHeader';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import { Button } from '@material-ui/core';
import EmojiPeopleIcon from '@material-ui/icons/EmojiPeople';
import CloseIcon from '@material-ui/icons/Close';
import frontend from '../actions/givers';
import DeleteInstDialog from '../components/Forms/DeleteInstDialog'
import FormDialogInst from '../components/Forms/formDialogInst';
import EditIcon from '@material-ui/icons/Edit';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      
    },
    card: {
      borderRadius: 10,
    },
    media: {
      height: "100%",
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
  }),
);


export default function RecipeReviewCard(props) {
    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);
    const [kept,setKept] = useState(false);
    const [capacity,setCapacity] = useState(0);
    const [maxCapacity,setMaxCapacity] = useState(0);
    const [deleteInstDialogOpen,setDeleteInstDialogOpen] = useState(false);
    const [editInstDialogOpen,setEditInstDialogOpen] = useState(false);

    function KeepButton(){
        if(kept) return  <BookmarkIcon/>;
        return <BookmarkBorderIcon/>;
    }
    
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const keep = () => {
    setKept(!kept);
    props.setAlert({
      description: "Evento Guardado!",
      severity:"info",
    });
    
    props.setOpen(true);
  };

  return (
    <>
    <DeleteInstDialog  aria-label="keep"  name ={props.inst.name.value} open={deleteInstDialogOpen} setOpen={setDeleteInstDialogOpen}/>
    <FormDialogInst open={editInstDialogOpen} setOpen={setEditInstDialogOpen} inst={props.inst}/>
     <Card className={classes.card}>
       
      <Grid container>
        <Grid item lg={4}>
          <CardMedia
            component="img"
            className={classes.media}
            image={props.inst.photo.value}
            title="Instituio"
          />
        </Grid>

        <Grid item lg={8}>
          
            
            <CardHeader
            style={{paddingBottom:0,paddingLeft:0}}
              action={
                <Grid container>
                  <Grid item>
                    <IconButton aria-label="settings" disabled={frontend.getUsername()!=props.inst.owner.value} onClick={()=>{setEditInstDialogOpen(true)}}>
                      <EditIcon />
                    </IconButton>
                  </Grid>
                  <Grid item>
                    <IconButton aria-label="settings" disabled={frontend.getUsername()!=props.inst.owner.value} onClick={()=>{setDeleteInstDialogOpen(true)}}>
                      <CloseIcon />
                    </IconButton>
                  </Grid>
                </Grid>
            }/><Box padding={3} paddingTop={0}>
              <Typography gutterBottom variant="h5" component="h2">
                {props.inst.name.value}
              </Typography>
              <Typography variant="body1" color="textSecondary" component="p">
                <br/>
                {"Morada: "+props.inst.address.value}
                <br/>
                {"Email: "+props.inst.email.value}
                <br/>            
                {"Telemvel: "+props.inst.phoneNr.value}

              </Typography>
            </Box>
        </Grid>
      </Grid>
    </Card>
    </>
  );
}

