import React, { useState } from 'react';
import { useHistory, Redirect } from 'react-router-dom';
import frontend from '../actions/givers';

import NavBar from '../navbar';
import Item from '../components/itemCard';
import { Grid } from '@material-ui/core';
import { useEffect } from 'react';


interface iItem {
  name: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
  },
  provider: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string
  },
  description: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
  },
  pricePer: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: number,
  },
  quantity: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: number,
  },
  photo: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
  },
}

function Profile() {
  
  const [items, setItems] = useState<iItem[]>([]);
  
  useEffect(() => {
    frontend.getShopItems().then((json) => {
 
      setItems(json);
      
    }).catch((e)=>{
      console.log(e.message);
    })
  }, []);

  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }
  

  return (
    <Grid container alignItems="stretch" alignContent="stretch"> 
      {items.map(function(i,index){
            return (
              <Grid item sm={2} key={index} style={{height: `100%`}}>
                <Item image={i.photo?i.photo.value:""} title={i.name.value} description={i.description.value} canBuy={true} cost={i.pricePer.value}/>
              </Grid>
            )
      })} 
    </Grid>
  );
}

export default Profile;