import { Button, FormControlLabel, Switch, TextField, Grid, Box, Card, IconButton } from '@material-ui/core';
import Tooltip from '@material-ui/core/Tooltip';
import InfoIcon from '@material-ui/icons/Info';
import React, { ChangeEvent, useState } from 'react'
import { Redirect } from 'react-router';
import frontend from '../../actions/giversBO';
import { useSnackbar } from 'notistack';

function CodeValue(){
    const [codeName,setCodeName] = useState("")
    const [value,setValue] = useState<number>(0)
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    function handleCodeName(e:ChangeEvent<HTMLInputElement>){
        setCodeName(e.target.value);
    }

    function handleCodeValue(e:ChangeEvent<HTMLInputElement>){
        setValue(parseInt(e.target.value));
    }


    function AddCodeValue(){
        frontend.AddCodeValue(codeName,value).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    function EditCodeValue(){
        frontend.EditCodeValue(codeName,value).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }
    
   if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }

    if(!frontend.isSuper() && !frontend.isBack()){
        return <Redirect to="/app/dashboard"/>
    }


    return(
        <>
            <Box height={window.innerHeight-155} display="flex" alignItems="center" justifyContent="center">
                <Card style={{width:"30%"}}>

                    <Grid container justifyContent="space-around" alignItems="center" spacing={2} style={{padding:15}}>
                        
                        <Grid item xl={12}>
                            <TextField
                                id="codeName"
                                label="Nome"
                                variant="outlined"
                                value={codeName}
                                onChange={handleCodeName}
                                fullWidth
                            />
                        </Grid>

                        <Grid item xl={12}>
                        <TextField
                            id="codeValue"
                            label="Valor"
                            variant="outlined"
                            value={value}
                            type="number"
                            onChange={handleCodeValue}
                            fullWidth
                        />
                        </Grid>
                        <Grid container item xl={12} direction="row">
                            <Grid item xl={1}>
                                <Tooltip title={"Indique o nome da varivel que quer adicionar ou editar, e o novo valor, que ser associado a essa varivel."}>
                                    <IconButton aria-label="">
                                        <InfoIcon />
                                    </IconButton>
                                </Tooltip>
                            </Grid >
                            <Grid item xl={11}>
                                <Box height="100%" width="100%" display="flex" alignItems="center" justifyContent="flex-end" >
                                    <Button style={{marginRight:5}} variant="contained" color="primary" onClick={EditCodeValue}>Editar</Button>
                                    <Button variant="contained" color="primary" onClick={AddCodeValue}>Adicionar</Button>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                    
                </Card>
            </Box>
  
        </>
    );

}

export default CodeValue;