import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import frontend from '../../actions/giversBO';
import { useSnackbar } from 'notistack';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

export default function SimpleCard(props) {
  const classes = useStyles();

    const [solved,setSolved] = React.useState(false)
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function MarkSolved(name){
    frontend.MarkSolved(props.name,props.type).then((text)=>{
        setSolved(true)
        enqueueSnackbar(text, { variant: 'success' });
    }).catch((error)=>{
        enqueueSnackbar(error.message, { variant: 'error' });
    })

}

  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
        {props.subType}
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          Informao: {props.text}
        </Typography>
        <Typography variant="body2" component="p">
          Utilizador: {props.username}
        </Typography>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
          Data: {props.creation_time}
        </Typography>
      </CardContent>
      <CardActions>
         <Button variant="contained" color="primary" onClick={MarkSolved} disabled={solved}>Resolvido</Button>
      </CardActions>
    </Card>
  );
}
