import React, {ChangeEvent} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper, { PaperProps } from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import { Badge, Grid, TextField } from '@material-ui/core';
import LoadingBtn from '../CustomButtons/LoadingButton';
import frontend from '../../actions/givers';
import { useSnackbar } from 'notistack';

import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';

import AddPhotoDialog from '../editProfilePic';

import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import { useHistory } from 'react-router';
function PaperComponent(props: PaperProps) {
  return (
      <Paper {...props} />
  );
}

export default function DraggableDialog(props) {
  const history = useHistory();
    const [text, setText] = React.useState(props.inst?props.inst.name.value:"");
    
    const [photo, setPhoto] = React.useState([]);
    const [photoDialogOpen, setPhotoDialogOpen] = React.useState(false);
    
    const [loading, setLoading] = React.useState(false);
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  
  function textHandler(e:ChangeEvent<HTMLInputElement>){
    setText(e.target.value);
  }


  const handleClose = () => {
    props.setOpen(false);
  };
  
  const handleSubmit = () => {
    setLoading(true)
    frontend.commentEvent(text,props.name,photo).then((text)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar(text, { variant: 'success' });
      window.location.reload();
    }).catch((error)=>{
      setLoading(false)
      handleClose();
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };

  const handleCancel = () => {
    handleClose();
  };
  return (
    <div>
    
      <Dialog
        open={props.open}
        onClose={handleClose}
        PaperComponent={PaperComponent}
        fullWidth
      >
        <AddPhotoDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhoto} profilePic={false}/>
        <DialogTitle style={{ cursor: 'default' }} id="draggable-dialog-title">
          Comentar Evento 
        </DialogTitle>
        <DialogContent>
        <TextField
            variant="outlined"
            margin="dense"
            fullWidth
            id="comment"
            label="Comentar"
            name="comment"
            value={text}
            multiline
            rows={4}
            onChange={textHandler}
          />
     
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleCancel} color="primary">
            Cancelar
          </Button>
          <Badge invisible={photo.length==0} color="secondary" badgeContent={""} showZero>
            <Button variant="contained" onClick={()=>setPhotoDialogOpen(true)} color="primary" disabled={loading}>
              <AddAPhotoIcon/>
            </Button>
          </Badge>
          <Button variant="contained" onClick={handleSubmit} color="primary" disabled={loading}>
            <LoadingBtn text={props.inst?"Editar":"Criar"} loading={loading}/>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}