import React from 'react';
import { Paper } from '@material-ui/core';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { useHistory, Link, Redirect } from 'react-router-dom';
import frontend from '../actions/givers';
import NavBar from '../navbar';
import Map from '../components/map';
import FormDialog from '../components/Forms/formDialog';
import { useEffect } from 'react';
import { Grid, Card } from '@material-ui/core';
import Form from '../components/Forms/formDialogTest';


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      
    },
    card:{
      borderRadius: 10,
    },
    paper: {
      width: `100%`,
      height: `100%`,

    },
    fab: {
      position: 'relative',
      bottom: theme.spacing(9),
      left: theme.spacing(2),
    },
  }),
);

function Profile() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [selectedMarker,setSelectedMarker] = React.useState();
  const [markers,setMarkers] = React.useState<number[]>([]);
  const [mapEvents,setMapEvents] = React.useState<number[]>([]);

  function OnLoad(lat,lng){
    frontend.getMapEventMarkers(lat,lng).then((list)=>{
      setMapEvents(list)
    })
  };

  useEffect(() => {
    if(!frontend.Authenticated()){
      <Redirect to="/"/>
    }
  }, []);

  function CreateEventForm(){
    if( frontend.isInst()){
      return(
      <Grid item xs={12} sm={12} md={4} xl={3}>
        <Form selectedMarker={selectedMarker} markers={markers}/>
      </Grid>
      );
    }
  }

  return (
    <>
      <Card className={classes.card}>
        <Grid container>

          <Grid item xs={12} sm={12} md={frontend.isInst()?8:12} xl={frontend.isInst()?9:12}>
            <Map height={frontend.isInst()?undefined:window.innerHeight-115} selectedMarker={selectedMarker} setSelectedMarker={setSelectedMarker} markers={markers} setMarkers={setMarkers} editable={frontend.isInst()} onLoad={OnLoad} mapEvents={mapEvents} setMapEvents={setMapEvents}/>
          </Grid>

          {CreateEventForm()}
          

        </Grid>
      </Card>
    </>
  );
}

export default Profile;

