import React from 'react';
import { Redirect } from 'react-router-dom';
import frontend from '../actions/givers';
import NavBar from '../navbar';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ScrollListInst from '../components/scrollListInst'
import { Button } from '@material-ui/core';
import FormDialogInst from '../components/Forms/formDialogInst';
import { Fab } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';

function Instituitions() {

  const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    
    },
    paper: {
      width: `100%`,
      height: `100%`,
    },
    fab: {
      position: 'absolute',
      top: theme.spacing(10),
      right: theme.spacing(2),
    },
  }),
);

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  return (
    <>  
      <ScrollListInst/>
      {!frontend.isInst() && <Fab color="primary" aria-label="add" className={classes.fab} onClick={handleClickOpen} style={{position: "fixed"}}>
          <AddIcon />
      </Fab>}
      <FormDialogInst open={open} setOpen={setOpen}/>
    </>
  );
}

export default Instituitions;