interface Badge{
  valueType: string,
  excludeFromIndexes: boolean,
  meaning: number,
  value: string,
}

interface CountryType {
  code: string;
  label: string;
  phone: string;
}

function countryToFlag(isoCode: string) {
  return typeof String.fromCodePoint !== 'undefined'
    ? isoCode
        .toUpperCase()
        .replace(/./g, (char) => String.fromCodePoint(char.charCodeAt(0) + 127397))
    : isoCode;
}

function UserProfile(props: any) {
  const { classes } = props;
  const classes2 = useStyles();
  let red = '#FF0000';

  const [profilePicDialogOpen,setProfilePicDialogOpen] = useState(false);
  const [passwordDialogOpen,setPasswordDialogOpen] = useState(false);
  const [deleteAccDialogOpen,setDeleteAccDialogOpen] = useState(false);

  const [address,setAddress] = useState<string>("");
  const [phone,setPhone] = useState<string>("");
  const [birth,setBirth] = useState<String>("");
  const [gender,setGender] = useState<string>("");
  const [email,setEmail] = useState<string>("");
  const [nationality,setNationality] = useState<string>("");
  const [bio,setBio] = useState<string>("");
  const [photo,setPhoto] = useState<string>(avatar);
  const [points,setPoints] = useState<number>();
  const [shopPoints,setShopPoints] = useState<number>();
  const [actions,setActions] = useState<number>();
  const [username,setUsername] = useState<string>("");
  const [name,setName] = useState<string>("");
  const [badges,setBadges] = useState<Badge[]>([]);

  const [loading,setLoading] = useState(false);

  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
 
  function emailHandler(e:ChangeEvent<HTMLInputElement>){
    setEmail(e.target.value);
  }
  function phoneHandler(e:ChangeEvent<HTMLInputElement>){
    setPhone(e.target.value);
  }
  function genderHandler(e:ChangeEvent<HTMLInputElement>){
    setGender(e.target.value);
  }
  function addressHandler(e:ChangeEvent<HTMLInputElement>){
    setAddress(e.target.value);
  }
  function birthHandler(e:ChangeEvent<HTMLInputElement>){
    setBirth(e.target.value);
  }
  function nationalityHandler(e:ChangeEvent<HTMLInputElement>){
    setNationality(e.target.value);
  }
  function bioHandler(e:ChangeEvent<HTMLInputElement>){
    setBio(e.target.value);
  }
  
  const OpenEditProfile = () => {
    setProfilePicDialogOpen(true);
  };

  const UpdateProfile = () => {
    setLoading(true)
    frontend.editProfile(address,new Date(birth+"").getTime(),bio,gender,[],nationality,phone).then((text)=>{
      setLoading(false)
      enqueueSnackbar(text, { variant: 'success' });
    }).catch((error)=>{
      setLoading(false); 
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };
  
  useEffect(() => { 
    if(!frontend.Authenticated()){
      <Redirect to="/"/>
    }
  }, []);
  
  useEffect(() => {
    frontend.getProfile("").then((user) => {
      let birthday = new Date(user.dateOfBirth.value);
    setUsername(user.username.value);
    setName(user.name.value);
    setAddress(user.address.value);
    setPhone(user.phoneNr.value);
    setBirth(birthday.getFullYear()+"-"+("0" + (birthday.getMonth() + 1)).slice(-2)+"-"+("0" + birthday.getDate()).slice(-2));
    setGender(user.gender.value);
    setEmail(user.email.value);
    setNationality(user.nationality.value);
    setBio(user.description.value);
    setPhoto(user.photo.value);
    setPoints(user.scorePoints.value);
    setShopPoints(user.shopPoints.value);
    setActions(user.eventCount.value);
    setBadges(user.badges.value);
   });
  }, []);

  return (
    <div style={{marginTop:`0%`}}>
      <ProfilePicDialog open={profilePicDialogOpen} setOpen={setProfilePicDialogOpen} setPhoto={setPhoto} profilePic={true}/>
      <PasswordDialog open={passwordDialogOpen} setOpen={setPasswordDialogOpen}/>
      <DeleteAccDialog open={deleteAccDialogOpen} setOpen={setDeleteAccDialogOpen}/>
      <Grid container spacing={3} >
        <Grid container item xs={12} sm={12} md={4} direction="column" spacing={4}>
          <Grid item>
            <Paper className={classes2.card}>
                <Box display="flex" justifyContent="center" alignItems="center" onClick={OpenEditProfile}>
                  <Avatar alt="Remy Sharp" src={photo} className={classes2.large} />
                </Box>
                <Box padding={3} paddingBottom={0} textAlign="center">
                  <h4 className={classes.cardTitle}>{username}</h4>
                  <Card style={{borderRadius: 10, width:'30%'}}>
                    <Box height="60px" style={{backgroundColor: '#ddd'}}>
                      <Grid container justifyContent="space-between">
                        {badges.map((badge,index)=>{
                          return( <Grid item xl={4}>
                          <CardMedia
                            key={index}
                            component="img"
                            alt="Badge"
                            className={classes2.media}
                            image={badge.value}
                            title={"Badge"}
                          />
                          </Grid>);
                          })}
                      </Grid>
                    </Box></Card>
                    <p className={classes.description}>
                      {bio}
                    </p>
                    <Divider/>
                    <Grid container>
                      <Grid item xs={4}>
                        <h3>
                          {shopPoints} <br />
                          <small>Givers</small>
                    
                        </h3>
                      </Grid>
                      <Grid item xs={4}>
                        <h3>
                          {actions} <br />
                          <small>Aes</small>
                        </h3>
                      </Grid>
                      <Grid item xs={4}>
                        <h3>
                          {points} <br />
                          <small>Pontos</small>
                        </h3>
                      </Grid>
                    </Grid>
                </Box>
              </Paper>
          </Grid>
          <Grid item>
            <Paper className={classes2.card}>
              <List component="nav" aria-label="main mailbox folders">
                <ListItem button onClick={()=>{setPasswordDialogOpen(true)}}>
                  <ListItemIcon> 
                    <LockIcon />
                  </ListItemIcon>
                  <ListItemText primary="Mudar Palavra Passe" />
                </ListItem>
                <ListItem button onClick={()=>{setDeleteAccDialogOpen(true)}}>
                  <ListItemIcon>
                    <ClearIcon style={{ color: red }}/>
                  </ListItemIcon>
                  <ListItemText primary="Apagar Conta" style={{ color: red }}/>
                </ListItem>
              </List>
            </Paper>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <Paper className={classes2.card}>
            <Box padding={2} paddingTop={3}>
              <Grid container spacing={3}>
                
                  <Grid item  xs={12} sm={12} md={4}>
                     <TextField
                    variant="outlined"
                    id="name"
                    label="Nome"
                    value={name}
                    disabled
                    fullWidth
                  />
                  </Grid>
                  <Grid item  xs={12} sm={12} md={8}>
                     <TextField
                    variant="outlined"
                    label="Email"
                    id="email-address"
                    value={email}
                    onChange={emailHandler}
                    disabled
                    fullWidth
                  />
                  </Grid>
                
                  <Grid item  xs={12} sm={12} md={4}>
                    <TextField
                      variant="outlined"
                      label="Data de Nascimento"
                      id="birthday"
                      type="date"
                      value={birth}
                      onChange={birthHandler}
                      InputLabelProps={{ shrink: true }}
                      fullWidth
                    />
                  </Grid>
                  <Grid item  xs={12} sm={12} md={4}>
                     <TextField
                        variant="outlined"
                        id="phone"
                        label="Telemovel"
                        value={phone}
                        onChange={phoneHandler}
                        type="number"
                        fullWidth
                      />
                  </Grid>
                  <Grid item  xs={12} sm={12} md={4}>
                    <TextField
                      variant="outlined"
                      label="Gnero"
                      id="gender"
                      value={gender}
                      select
                      onChange={genderHandler}
                      fullWidth
                    >
                      {genders.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </TextField>
                  </Grid>
               
                
                  <Grid item  xs={12} sm={12} md={8}>
                     <TextField
                        variant="outlined"
                        id="address"
                        label="Morada"
                        value={address}
                        onChange={addressHandler}
                        fullWidth
                      />
                  </Grid>
                  <Grid item  xs={12} sm={12} md={4}>
                    <TextField
                      variant="outlined"
                      label="Pas"
                      id="country"
                      value={nationality}
                      onChange={nationalityHandler}
                      fullWidth
                    />
                  </Grid>
                
                  <Grid item  xs={12} sm={12} md={12}>
                     <TextField
                        variant="outlined"
                        id="bio"
                        label="Biografia"
                        value={bio}
                        onChange={bioHandler}
                        multiline
                        rows={4}
                        fullWidth
                        error={bio.length==300}
                        helperText={bio.length==300 ? 'Mximo 300 caracteres 300/300' : ''}
                        inputProps={{ maxLength: 300 }} 
                      />
                  </Grid>
                
              </Grid>
            </Box>
            <Box padding={2} paddingBottom={2}>
              <Button color="primary" onClick={UpdateProfile} disabled={loading}><LoadingBtn text={"Editar"} loading={loading}/></Button>
            </Box>
          </Paper>
        </Grid>
     </Grid>
    </div>
  );
}

export default withStyles(styles)(UserProfile);
