import React, {ChangeEvent} from 'react';
import Button from '@material-ui/core/Button';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Badge, Grid, MenuItem, TextField } from '@material-ui/core';
import frontend from '../../actions/givers';
import { Divider } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import LoadingBtn from '../CustomButtons/LoadingButton'

import AddPhotoDialog from '../../components/editProfilePic';

export default function DraggableDialog(props) {
  const date = new Date();
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const [address, setAddress] = React.useState("");
  const [capacity, setCapacity] = React.useState(0);
  const [dateStart, setDateStart] = React.useState("2021-07-09T10:00");
  const [interests, setInterests] = React.useState("");
  const [duration, setDuration] = React.useState("00:00");
  const [description, setDescription] = React.useState("");
  const [name, setName] = React.useState("");
  const [lat, setLat] = React.useState(0);
  const [long, setLong] = React.useState(0);
  
  const [photoDialogOpen,setPhotoDialogOpen] = React.useState(false);
  const [photo,setPhoto] = React.useState([]);
  
  const [loading,setLoading] = React.useState(false);


  function addressHandler(e:ChangeEvent<HTMLInputElement>){
    setAddress(e.target.value);
  }
  
  function capacityHandler(e:ChangeEvent<HTMLInputElement>){
    setCapacity(isNaN(parseInt(e.target.value))?0:parseInt(e.target.value));
  }
  
  function dateStartHandler(e:ChangeEvent<HTMLInputElement>){
    setDateStart(e.target.value);
  }
  
  function interestsHandler(e:ChangeEvent<HTMLInputElement>){
    setInterests(e.target.value);
  }
  
  function durationHandler(e:ChangeEvent<HTMLInputElement>){
    setDuration(e.target.value);
  }
  
  function descriptionHandler(e:ChangeEvent<HTMLInputElement>){
    setDescription(e.target.value);
  }
  
  function nameHandler(e:ChangeEvent<HTMLInputElement>){
    setName(e.target.value);
  }
  
  function latHandler(e:ChangeEvent<HTMLInputElement>){
    setLat(isNaN(parseInt(e.target.value))?0:parseInt(e.target.value));
  }
  
  function longHandler(e:ChangeEvent<HTMLInputElement>){
    setLong(isNaN(parseInt(e.target.value))?0:parseInt(e.target.value));
  }

  function Clean(){
    setAddress("")
    setCapacity(0)
    setDateStart("2021-07-09T10:00")
    setInterests("")
    setDuration("00:00")
    setDescription("")
    setLat(0)
    setLong(0)
  }

  const handleSubmit = () => {
    setLoading(true)
    frontend.registEvent(address,capacity,description,duration,interests,new Date(dateStart).getTime(),name,props.markers,photo).then((text)=>{
      setLoading(false)
      enqueueSnackbar(text, { variant: 'success' });
      Clean()
    }).catch((error)=>{
      setLoading(false)
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  };

  return (    
    <div style={{height:'100%'}}>
        <AddPhotoDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhoto} profilePic={false}/>
        <DialogTitle style={{ cursor: 'Default' }} id="draggable-dialog-title">
          Criar Evento
        </DialogTitle>
        <Divider/>
        <DialogContent>
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="name"
            label="Nome do Evento"
            name="name"
            autoFocus
            value={name}
            onChange={nameHandler}
          />

        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="address"
            label="Morada"
            name="adress"
            autoComplete="adress"
            autoFocus
            value={address}
            onChange={addressHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="capacity"
            label="Capacidade"
            name="capacity"
            autoComplete="capcity"
            autoFocus
            value={capacity}
            onChange={capacityHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="interests"
            label="Interesse"
            name="interests"
            autoComplete="interests"
            autoFocus
            value={interests}
            onChange={interestsHandler}
            select
          >
            {frontend.getInterests().map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
          <TextField
              variant="outlined"
              margin="normal"
              fullWidth
              id="dateStart"
              label="Data de incio"
              name="dateStart"
              type="datetime-local"
              autoComplete="dateStart"
              autoFocus
              value={dateStart}
              onChange={dateStartHandler}
            />
              <TextField
            id="time"
            label="Durao (HH:MM)"
            type="time"
            margin="normal"
            fullWidth
            variant="outlined"
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{
              step: 300, // 5 min
            }}
            value={duration}
            onChange={durationHandler}
          />
        <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="description"
            label="Descrio do evento"
            name="description"
            autoFocus
            multiline
            rows={5}
            value={description}
            onChange={descriptionHandler}
          />
        
          <Grid container justify='space-around'>
          <Grid item xs={6} sm={4}>
        <TextField
            disabled
            variant="outlined"
            margin="normal"
            fullWidth
            id="lat"
            label="Latitude"
            name="lat"
            autoFocus
            value={props.markers[0]==undefined? 0.0:props.markers[0].lat/*props.selectedMarker.lat()*/}
          />
          </Grid>
          <Grid item xs={6} sm={4}>
        <TextField
            disabled
            variant="outlined"
            margin="normal"
            fullWidth
            id="long"
            label="Longitude"
            name="long"
            autoFocus
            value={props.markers[0]==undefined? 0.0:props.markers[0].lng/*props.selectedMarker.lng()*/}
          /></Grid>
          </Grid>
        </DialogContent>
        <Divider/>
        <DialogActions>
  
          <Button variant="text" disabled color="primary">
          {props.markers.length+" Marcadores"}
          </Button>

          <Badge invisible={photo.length==0} color="secondary" badgeContent={""} showZero>
            <Button variant="contained" onClick={()=>setPhotoDialogOpen(true)} color="primary" disabled={loading}>
              <AddAPhotoIcon/>
            </Button>
          </Badge>
          <Button variant="contained" onClick={handleSubmit} color="primary" disabled={loading}>
            <LoadingBtn text={"Criar"} loading={loading}/>
          </Button>
        </DialogActions>
      </div>
  );
}