import { Grid, Fab, makeStyles, Theme, createStyles  } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react'
import ShopItem from '../Components/itemCardBO'

import AddIcon from '@material-ui/icons/Add';
import frontend from '../../actions/giversBO';


import AddItemDialog from '../Form/AddItemDialog'
import { Redirect } from 'react-router';
import { useSnackbar } from 'notistack';

interface TabPanelProps {
    children?: React.ReactNode;
    index: any;
    value: any;
  }

interface iItem {
name: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
},
provider: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string
},
description: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
},
pricePer: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: number,
},
quantity: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: number,
},
photo: {
    valueType: string,
    excludeFromIndexes: boolean,
    meaning: number,
    value: string,
},
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      
    },
    paper: {
      width: `100%`,
      height: `100%`,
      overflow: 'scroll'
    },
    fab: {
      position: 'absolute',
      bottom: theme.spacing(7),
      right: theme.spacing(7),
      
    },
}),
);

function ShopItems(){
    
    
    const classes = useStyles();
    const [itemName,setItemName] = useState("")
    const [provider,setProvider] = useState("")
    const [description,setDescription] = useState("")
    const [price,setPrice] = useState(0)
    const [quantity,setQuantity] = useState(0)
    const [photo,setPhoto] = useState([])
    const [items,setItems] = useState<iItem[]>([])
    
    const [photoDialogOpen,setPhotoDialogOpen] = React.useState(false);
    const [addDialogOpen,setAddDialogOpen] = React.useState(false);
    
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    React.useEffect(()=>{
        GetAllShopItems()
    },[])

       
    if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }
    
    if(!frontend.isSuper() && !frontend.isBack()){
        return <Redirect to="/app/dashboard"/>
    }

    function handleItemName(e:ChangeEvent<HTMLInputElement>){
        setItemName(e.target.value);
    }

    function handleProvider(e:ChangeEvent<HTMLInputElement>){
        setProvider(e.target.value);
    }

    function handleDescription(e:ChangeEvent<HTMLInputElement>){
        setDescription(e.target.value);
    }

    function handlePrice(e:ChangeEvent<HTMLInputElement>){
        setPrice(Math.max(1,parseInt(e.target.value)));
    }

    function handleQuantity(e:ChangeEvent<HTMLInputElement>){
        setQuantity(Math.max(0,parseInt(e.target.value)));
    }



    function AddShopItem(itemName, provider, description, price, quantity,photo){
        return frontend.AddShopItem(itemName, provider, description, price, quantity,photo).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
            GetAllShopItems()
            setAddDialogOpen(false)
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }
    
    function DeleteShopItem(itemName,index){
        frontend.DeleteShopItem(itemName).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
            items.splice(index,1)
            setItems([])
            setItems(items)
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    function EditShopItem(itemName, description, price, quantity,photo){
        frontend.EditShopItem(itemName, description, price, quantity,photo).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
            GetAllShopItems()
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    function GetAllShopItems(){
        frontend.GetAllShopItems().then((items)=>{
            setItems([])
            setItems(items)
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }


    return(
        <>
        <AddItemDialog open={addDialogOpen} setOpen={setAddDialogOpen} action={AddShopItem}/>

        <Grid container>
            {items.map((item,index)=>{
                return(<Grid item key={index}><ShopItem index={index}  item={item} edit={EditShopItem} delete={DeleteShopItem}/></Grid>);
                
            })}
        </Grid>
        
        <Fab color="primary" aria-label="edit" style={{position: "fixed",}} className={classes.fab} onClick={()=>{setAddDialogOpen(true)}}>
            <AddIcon />
        </Fab>
            
        </>
    );

}

export default ShopItems;