import React, {ChangeEvent, useState} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper, { PaperProps } from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import { Badge, Grid, TextField } from '@material-ui/core';
import frontend from '../../actions/giversBO';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import { Card, Divider } from '@material-ui/core';
import { Typography } from '@material-ui/core';

import LoadingBtn from '../../components/CustomButtons/LoadingButton'

import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';

import AddPhotoDialog from '../../components/editProfilePic';

export default function DraggableDialog(props) {
  const [itemName,setItemName] = useState("")
  const [provider,setProvider] = useState("")
  const [description,setDescription] = useState("")
  const [price,setPrice] = useState(0)
  const [quantity,setQuantity] = useState(0)
  const [photo,setPhoto] = useState([])
  
  const [loading,setLoading] = useState(false)
  const [photoDialogOpen,setPhotoDialogOpen] = React.useState(false);

  function handleItemName(e:ChangeEvent<HTMLInputElement>){
    setItemName(e.target.value);
}

function handleProvider(e:ChangeEvent<HTMLInputElement>){
    setProvider(e.target.value);
}

function handleDescription(e:ChangeEvent<HTMLInputElement>){
    setDescription(e.target.value);
}

function handlePrice(e:ChangeEvent<HTMLInputElement>){
    setPrice(Math.max(1,parseInt(e.target.value)));
}

function handleQuantity(e:ChangeEvent<HTMLInputElement>){
    setQuantity(Math.max(0,parseInt(e.target.value)));
}
  
function AddShopItem(){
  setLoading(true);
  props.action(itemName, provider, description, price, quantity,photo).then((text)=>{
    setLoading(false)
    setItemName("")
    setProvider("")
    setDescription("")
    setPhoto([])
    setPrice(0)
    setQuantity(0)
  })
}

  return (    
      <Dialog open={props.open} onClose={()=>{props.setOpen(false)}}>
        <AddPhotoDialog open={photoDialogOpen} setOpen={setPhotoDialogOpen} setPhoto={setPhoto} profilePic={false}/>
        <DialogTitle style={{ cursor: 'Default' }} id="draggable-dialog-title">
          Adicionar Item
        </DialogTitle>
        <Divider/>
        <DialogContent>
          <Grid container spacing={2}>
            <Grid item xl={12}>
              <TextField
                id="itemName"
                label="Nome"
                variant="outlined"
                value={itemName}
                onChange={handleItemName}
                fullWidth
              />
            </Grid>

            <Grid item xl={12}>
              <TextField
                id="provider"
                label="Fornecedor"
                variant="outlined"
                value={provider}
                onChange={handleProvider}
                fullWidth
              />
            </Grid>

            <Grid item xl={12}>
              <TextField
                id="description"
                label="Descrio"
                variant="outlined"
                value={description}
                onChange={handleDescription}
                fullWidth
              />
            </Grid>

            <Grid item xl={6}>
              <TextField
                id="price"
                label="Preo"
                variant="outlined"
                value={price}
                type="number"
                onChange={handlePrice}
                fullWidth
              />
            </Grid>

            <Grid item xl={6}>
              <TextField
                id="quantity"
                label="Quantidade"
                variant="outlined"
                value={quantity}
                onChange={handleQuantity}
                fullWidth
                type="number"
              />
            </Grid>
          </Grid>
            
        </DialogContent>
        <Divider/>
        <DialogActions>
          <Badge invisible={photo.length==0} color="secondary" badgeContent={""} showZero>
            <Button variant="contained" onClick={()=>setPhotoDialogOpen(true)} color="primary">
              <AddAPhotoIcon/>
            </Button>
          </Badge>
          <Button variant="contained" onClick={AddShopItem} color="primary" disabled={loading}>
            <LoadingBtn text={"Adicionar"} loading={loading}/> 
          </Button>
        </DialogActions>
      </Dialog>
  );
}