import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';

import PhotoIcon from '@material-ui/icons/Photo';
import CloseIcon from '@material-ui/icons/Close';

import { useSnackbar } from 'notistack';
import frontend from '../actions/givers';
import { Button, Grid } from '@material-ui/core';

import Comment from '../components/commentCard';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
     width: `100%`
    },
    media: {
      height: 0,
      paddingTop: '18.75%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
  }),
);

export default function RecipeReviewCard(props) {
  const classes = useStyles();
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  const [comments,setComments] = useState(props.comments);
  const [commentCursor,setCommentCursor] = useState(props.cursor);
  const [loadingComments,setLoadingComments] = useState(false);

  
  function RemoveComment(index,id,event){
    frontend.deleteEventComment(id,event).then((text)=>{
      enqueueSnackbar(text, { variant: 'success' });
      comments.splice(index,1)
      setComments(comments.concat([]))
    })
  }

  function RequestComments(){
    if(commentCursor!="end"){
      frontend.getComments(props.name,commentCursor,true).then((commentList)=>{
          setComments(comments.concat(commentList.list));
          setCommentCursor(commentList.cursor);
      });
    }
  }

  return (
    <Grid container spacing={2}>

      {comments.map((comment,index)=> (
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12} key={index}>
          
          
          <Comment comment={comment}  index={index} remove={RemoveComment} event={props.name}/>
        </Grid>
      ))}

      {commentCursor!="end" && <Button disabled={loadingComments} onClick={RequestComments}>Carregar Mais</Button>}
      {comments.length==0 && <Typography>No existem Comentrios</Typography>}
    </Grid>
  );
}