import React, { useEffect } from 'react';
import { Redirect } from 'react-router';
import frontend from './actions/givers';
import { Grid, Card, Box, Paper, List } from '@material-ui/core';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import UserEventCard from './components/Card/UserEventCard';
import SavedEventCard from './components/Card/SavedEventCard';
import FutureEventCard from './components/Card/FutureEventCard';
import { Event } from '@material-ui/icons';
import CSS from 'csstype';
import { CSSProperties } from '@material-ui/core/styles/withStyles';


interface Event {
    "address": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "capacity": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "date_end": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "date_start": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "description": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "photo": {
      "excludeFromIndexes": false,
      "meaning": number,
      "value": string,
      "valueType": string
  },
    "institution": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "interests": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "name": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "participants": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": [],
      "valueType": string
    },
    "points_rewarded": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": boolean,
      "valueType": string
  }

}

const URL = "https://givers-volunteering.appspot.com/rest/query/eventsFiltered";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      height: '100%',
      padding: theme.spacing(1),
      textAlign: 'center',
      color: 'theme.palette.text.secondary',
    },
  }),
);


function Home() {
  const classes = useStyles();
  
  const [userFutureEvents,setUserFutureEvents] = React.useState<Event[]>([]);
  const [userPastEvents,setUserPastEvents] = React.useState<Event[]>([]);
  const [userOnGoingEvents,setUserOnGoingEvents] = React.useState<Event[]>([]);
  const [userSavedEvents,setUserSavedEvents] = React.useState<Event[]>([]);
  const [hasOnGoingEvents, setHasOnGoingEvents] = React.useState(false)
  const [hasPastEvents, setHasPastEvents] = React.useState(false)
  const [hasFutureEvents, setHasFutureEvents] = React.useState(false)
  const [hasSavedEvents, setHasSavedEvents] = React.useState(false)


  const [cursor,setCursor] = React.useState("");

  const titleStyle: CSSProperties = {
    color: 'white',
    fontWeight: 'bold',
    fontSize: '20px',
    padding: '3px 10px',
    backgroundColor: '#84CEEB',
    borderRadius: '5px',
    textAlign : 'left'

  };
  
  const noneStyle: CSSProperties = {
    textAlign: 'left',
    fontSize: '15px',
    padding: '3px 10px',
    color: '#4D6D9A',
    borderRadius: '5px'
  }

  useEffect(()=>{
    frontend.getUserEvents(-1).then((events)=>{
      if(events.length==0){
        setHasPastEvents(false)
      } else{
      setUserPastEvents(events);
      setHasPastEvents(true);
      }
    })

    frontend.getUserEvents(1).then((events)=>{
      if(events.length==0){
        setHasFutureEvents(false);
      } else{
         setUserFutureEvents(events);
         setHasFutureEvents(true);
      }
    
    })

    frontend.getUserEvents(2).then((events)=>{
      if(events.length==0){
        setHasOnGoingEvents(false)
      }
      else{
        setUserOnGoingEvents(events);
        setHasOnGoingEvents(true)
      }
    
    })

    frontend.getSavedEvents().then((events)=>{
      if(events.length==0){
        setHasSavedEvents(false);
      }  else{
        setUserSavedEvents(events)
        setHasSavedEvents(true);
      }
    })

  },[]);
  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }

  const handleRemoveSavedEvent = (name) => {
    setUserSavedEvents(userSavedEvents.filter(event => event.name !== name));
   };
 

  return (
    <div className={classes.root}>
      <Grid container spacing={3} style={{height:'89vh', display: 'inline-block', minWidth:'200'}}>
      <h3 style={titleStyle}>Eventos a Decorrer</h3>
        <Grid container item spacing={3} >
          {!hasOnGoingEvents && <p style =  {noneStyle}>No tem nenhum evento a decorrer</p>}
          {userOnGoingEvents.map((event, ix) => (
            <Grid item xs={12} sm= {6} lg={6}  key={ix}>
          <FutureEventCard style={{minWidth:'300px'}}
            name={event.name.value}
            description={event.description.value}
            date_start={event.date_start.value}
            address={event.address.value}
            photo = {event.photo.value}
          /> </Grid>)) }
        </Grid>
        <h3 style={titleStyle}>Prximos Eventos</h3>
        <Grid container  spacing={3} >
        {!hasFutureEvents && <p style={noneStyle}>No est inscrito em nenhum evento futuro, de que est  espera para ser um Giver?</p>}
          {userFutureEvents.map((event, ix) => (
          <Grid item xs={12} sm= {6} lg={6}  key={ix}>
          <FutureEventCard style={{minWidth:'300px'}}
            key={ix}
            name={event.name.value}
            description={event.description.value}
            date_start={event.date_start.value}
            address={event.address.value}
            photo = {event.photo.value}

          />
          </Grid>))}
        </Grid>
        <h3 style={titleStyle}>Eventos Guardados</h3>
        <Grid container   spacing={3} >
        {!hasSavedEvents && <p style={noneStyle}>No tem nenhum evento guardado, de certeza que vai encontrar algo para si!</p>}
          {userSavedEvents.map((event, ix) => (
            <Grid item xs={12} sm= {6} lg={6}   key={ix}>
          <SavedEventCard style={{minWidth:'300px'}}
            key={ix}
            name={event.name.value}
            description={event.description.value}
            date_start={event.date_start.value}
            address={event.address.value}
            photo = {event.photo.value}
            handleRemoveSavedEvent = {handleRemoveSavedEvent}
          />
          </Grid>))}
        </Grid>
        <h3 style={titleStyle} >Eventos Passados</h3>
        {!hasPastEvents && <p style={noneStyle}>No tem nenhum evento feito, junte-se a um e torne-se um Giver!</p>}
        <Grid container spacing={3}>
        {userPastEvents.map((event, ix) => (
          <Grid item xs={12} sm= {6} lg={6}   >
            <UserEventCard style={{minWidth:'300px'}}
              key={ix}
              name={event.name.value}
              description={event.description.value}
              date_start={event.date_start.value}
              address={event.address.value}
              photo = {event.photo.value}

            />
           </Grid>))}

        </Grid>
        
      </Grid>
    </div>


  );
}

export default Home;

//Checked