import React, { useState, useEffect } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import Grid from '@material-ui/core/Grid';
import GroupButton from '../components/CustomButtons/GroupButton';
import OptionButton from '../components/CustomButtons/OptionButton';
import ControlOpenSelect from '../components/CustomInput/ControlOpenSelect';
import Event from '../components/eventCard';
import frontend from "../actions/givers";

//import Alert from './alert';
import { CircularProgress, Button } from "@material-ui/core";

const style = {
  width: 1000,
  overflow: "hidden",
};
const All = {
  display: "flex",
  justifyContent: "center",
  marginTop:0,
};



function Scroll(){
  const [cursor, setCursor] =useState("");
  const [alert, setAlert] = useState({
    description: "test",
    severity:"warning",
  });
  const [open, setOpen] = useState(false);

  const [items, setItem] = useState([]);

  const [interest, setInterest] = useState("");

  const [timeFilter, setTimeFilter] = useState(0);


  useEffect(() => {
    // Initialization code comes here
      frontend.listAllEvents(timeFilter,interest,"").then(function(json) {
        setCursor(json.cursor);
        setItem(items.concat(json.list));
      }); 
  }, []);



  const fetchMoreData = () => {
      frontend.listAllEvents(timeFilter,interest,cursor).then(function(json) {
        setCursor(json.cursor);
        setItem(items.concat(json.list));
      });
  };

  const handleChangeInterest  = (newInterest) => {
    setInterest(newInterest);
    frontend.listAllEvents(timeFilter,newInterest,"").then(function(json) {
      setCursor(json.cursor);
      setItem(json.list);
    }); 
  };

  const handleChangeTime  = (newTimeFilter) => {
    setTimeFilter(newTimeFilter);
      frontend.listAllEvents(newTimeFilter,interest,"").then(function(json) {
      setCursor(json.cursor);
      setItem(json.list);
    }); 
  };

  const EndMessage = () => {
    if(items.length == 0){
      return <b>No h eventos para mostrar</b>;
    }
    else{
      return <b>Boa! J viste tudo</b>;
    }
  }

    return (
      <div style={All}>
        
      <Grid container>
        <Grid item xs={12} sm={12} md={3}>
        <OptionButton 
          onChangeTimeFilter = {handleChangeTime}>
           
        </OptionButton>
        <ControlOpenSelect onChangeInterest = {handleChangeInterest}/>
        </Grid>
        <Grid item xs={12} sm={12} md={9}>

      
        <InfiniteScroll style={style}
          dataLength={items.length}
          next={fetchMoreData}
          hasMore={cursor!="end"}
          loader={<div style={{ textAlign: "center", margin: `3% 0`}}>
          <CircularProgress  variant="indeterminate" color={"primary"}/>
        </div>}
          endMessage={
              <p style={{ textAlign: "center" ,paddingTop: "5px"}}>
              {EndMessage()}
            </p>
          }
          >
          <Grid container spacing={3}>
        
          {items.map((e, index) => (
                <Grid key={index} item sm={12}>
                    <Event index={index} style={style} event={e} setAlert={setAlert} setOpen={setOpen} />
                </Grid>    
          ))}
          </Grid>
        </InfiniteScroll>
        </Grid>
      </Grid>
        {/*<Alert description={alert.description} severity={alert.severity} open={open} setOpen={setOpen}/>*/}
        </div>
    );
}

export default Scroll;
 /*<div style={style} key={index}>
              div - #{index}
            </div>*/