import React from "react";
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
// core components
import Sidebar from "./drawerBO";

import routes from "./routesBO";

const Admin = (props) => {
  const mainContent = React.useRef(null);
  const location = useLocation();

  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement!.scrollTop = 0;
  }, [location]);

  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/backoffice") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  return (
    <>
      <Sidebar
        {...props}
        routes={routes}
        logo={{
          innerLink: "/ ",
          imgSrc: require("../assets/white.png").default,
          imgAlt: "...",
        }}
      page={<div className="main-content" ref={mainContent}>
        <Switch>
          {getRoutes(routes)}
          <Redirect from="*" to="/backoffice/databaseLogs" />
        </Switch>
      </div>}
      />
    </>
  );
};

export default Admin;
