import React, { ChangeEvent } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { CircularProgress, Link, Typography } from '@material-ui/core';
import frontend from '../../actions/givers';
import { useSnackbar } from 'notistack';

export default function FormDialog() {
  const [open, setOpen] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [email, setEmail] = React.useState("");
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  function emailHandler(e:ChangeEvent<HTMLInputElement>){
    setEmail(e.target.value);
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleSubmit = () => {
      setLoading(true)
      frontend.SendForgotPassword(email).then((message)=>{
        setLoading(false)
        enqueueSnackbar(message, { variant: 'success' });
        setOpen(false);
    }).catch((error)=>{
        setLoading(false)
        enqueueSnackbar(error.message, { variant: 'error' });
    })
    
  };

  function LoadingBtn(){
    if(!loading){
      return <>Enviar</>;
    }else
    return <CircularProgress size={24} variant="indeterminate" color={"inherit"}/>;
  }


  return (
    <div>
        <Link onClick={handleClickOpen} variant="body2" style={{cursor: "pointer"}}>
            Esqueci-me da password
        </Link>
      <Dialog open={open} onClose={()=>{setOpen(false)}} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Redifinir Password</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Ir ser enviado um email de confirmao com um link para prosseguir com o pedido. 
          </DialogContentText>
          <TextField
            autoFocus
            variant="outlined"
            id="email"
            label="Email"
            type="email"
            fullWidth
            value={email}
            onChange={emailHandler}
          />
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={()=>{setOpen(false)}} color="primary">
            Cancelar
          </Button>
          <Button variant="contained" onClick={handleSubmit} color="primary" disabled={loading}>
            <LoadingBtn/>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}