import { Button, FormControl, FormControlLabel, Grid, InputLabel, MenuItem, Select, Switch, TextField } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react'
import { DataGrid, GridColDef, GridValueGetterParams } from '@material-ui/data-grid';
import frontend from '../../actions/giversBO';
import { Redirect } from 'react-router';
import { useSnackbar } from 'notistack';

interface Entry{
    id: number,
    username: string,
    date: string ,
    name: string,
    method: string
}

const columns: GridColDef[] = [
    {
      field: 'id',
      headerName: 'ID',
      width: 100,
      editable: false,
    },
    {
      field: 'method',
      headerName: 'Mtodo',
      flex: 1,
      editable: false,
    },
    {
      field: 'username',
      headerName: 'Username',
      flex: 1,
      editable: false,
    },
    {
      field: 'name',
      headerName: 'Nome',
      flex: 1,
      editable: false,
    },
    {
      field: 'date',
      headerName: 'Data',
      type: 'string',
      flex: 1,
      editable: false,
    }
  ];

  const rows = [
    {  id: 1, username: 'Jon', date: 35 },
    {  id: 2, username: 'Cersei', date: 42 },
    {  id: 3, username: 'Jaime', date: 45 },
    {  id: 4, username: 'Arya', date: 16 },
    {  id: 5, username: 'Daenerys', date: null },
    {  id: 6, username: null, date: 150 },
    {  id: 7, username: 'Ferrara', date: 44 },
    {  id: 8, username: 'Rossini', date: 36 },
    {  id: 9, username: 'Harvey', date: 65 },
  ];

function DatabaseLogs(){
    const [method,setMethod] = useState("")
    const [time,setTime] = useState(0)
    const [queryTime,setQueryTime] = useState(0)
    const [entryList,setEntryList] = useState<Entry[]>([])
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    function handleMethod(e:ChangeEvent<HTMLInputElement>){
        setMethod(e.target.value);
    }

    function handleTime(e:ChangeEvent<HTMLInputElement>){
        setTime(parseInt(e.target.value));
    }

    function handleQueryTime(e:ChangeEvent<HTMLInputElement>){
        setQueryTime(parseInt(e.target.value));
    }

    const [age, setAge] = React.useState('');

    const handleChange = (event) => {
        setMethod(event.target.value);
        GetLogs(event.target.value, time, queryTime)
    };


    function GetLogs(method, time, queryTime){
        frontend.GetLogs(method).then((list)=>{
            var entrySet:Entry[] = []
            list.map((entry,index)=>{
                var date = new Date(entry.time.value)
                entrySet.push({id: index, method: entry.method.value, username: entry.username.value, date: date.toDateString() +" "+  date.toTimeString(), name: entry.name.value  })
            })
           setEntryList(entrySet)
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }


    
    if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }
    
    if(!frontend.isSuper() && !frontend.isBack()){
        return <Redirect to="/app/dashboard"/>
    }

    return(
        <>
        
            <FormControl style={{paddingBottom:10, minWidth:120}}>
                <InputLabel id="method">Mtodo</InputLabel>
                <Select
                    labelId="method"
                    id="method"
                    value={method}
                    onChange={handleChange}
                >
                    <MenuItem value={""}>All</MenuItem>
                    <MenuItem value={"registerUser"}>registerUser</MenuItem>
                    <MenuItem value={"registerInstitution"}>registerInstitution</MenuItem>
                    <MenuItem value={"registerEvent"}>registerEvent</MenuItem>
                    <MenuItem value={"registerShopItem"}>registerShopItem</MenuItem>
                    <MenuItem value={"deleteUser"}>deleteUser</MenuItem>
                    <MenuItem value={"deleteInstitution"}>deleteInstitution</MenuItem>
                    <MenuItem value={"deleteEvent"}>deleteEvent</MenuItem>
                    <MenuItem value={"deleteShopItem"}>deleteShopItem</MenuItem>
                </Select>
            </FormControl>

            <Grid style={{ height: window.innerHeight-160, width: '100%' }}>
                <DataGrid
                    rows={entryList}
                    columns={columns}
                    pageSize={10}
                    autoHeight
                    disableSelectionOnClick
                />
            </Grid>
           
     </>
    );

}

export default DatabaseLogs;