import React, { useState }from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import frontend from '../../actions/givers';
import LeaveEventDialog from '../Forms/LeaveEventDialog';
import { useHistory } from 'react-router';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
});


export default function ImgMediaCard(props:any) {
  const classes = useStyles();
  const name = props.name;
  const description= props.description;
  const date_start= new Date(props.date_start).toLocaleString([], { timeZone: 'UTC',year: 'numeric', month: 'numeric', day: 'numeric', hour: '2-digit', minute: '2-digit' })+""
  const address = props.adress;
  const photo = props.photo;
  const [kept,setKept] = useState(true);
  const history = useHistory()



   
  return (
    <Card className={classes.root} >
      <CardActionArea>
      <CardMedia
          component="img"
          alt="Fotografia do Evento"
          height="140"
          image={photo}
        />
        <CardContent>
          <Typography gutterBottom variant="h6" component="h2">
            {name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {address}
          </Typography>
          <Typography variant="body2" style={{color: 'black'}} component="p">
            {date_start}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" style={{backgroundColor: '#84CEEB', color: 'white'}}  onClick={()=>{history.push('event/'+name.replaceAll(' ',""))}}>
          Saber Mais
        </Button>
        <LeaveEventDialog aria-label="keep"  name ={props.name}/>
     
      </CardActions>
    </Card>
  );
}
