import { Grid, Button, FormControl, FormControlLabel, InputLabel, MenuItem, Select, Switch, TextField } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react'
import { Redirect } from 'react-router';
import frontend from '../../actions/giversBO';
import ReportCard from '../Components/reportCard';

interface ticket {
    name: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string,
    },
    subtype: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string,
    },
    text: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string
    },
    username: {
        valueType: string,
        excludeFromIndexes: boolean,
        meaning: number,
        value: string,
    },
    creation_time: {
        valueType: number,
        excludeFromIndexes: boolean,
        meaning: number,
        value: number,
    }
}

function SuggestionReportBug(){
    const [type,setType] = useState<string>("")
    const [list,setList] = useState<ticket[]>([])


   const handleChange = (e:ChangeEvent<HTMLInputElement>) => {
        setType(e.target.value);
        frontend.GetSuggRepBug(e.target.value).then((items)=>{
            setList(items);
        })
    };


    if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }

    if(!frontend.isSuper() && !frontend.isBack()){
        return <Redirect to="/app/dashboard"/>
    }


    return(
        <> 
            <FormControl style={{minWidth: 120, paddingBottom:10}}>
                <TextField
                    label="Tipo"
                    id="types"
                    value={type}
                    select
                    onChange={handleChange}
                >
                    <MenuItem value={"SUGGESTION"}>Sugestes</MenuItem>
                    <MenuItem value={"REPORT"}>Denncias</MenuItem>
                    <MenuItem value={"BUG"}>Bugs</MenuItem>
                </TextField>
            </FormControl>
            <Grid container spacing={3}>
                {list.map((report, ix) => (
                    
                <Grid key={ix} item xs={12} sm= {6} lg={3}   >
                    <ReportCard style={{minWidth:'300px'}}
                        name= {report.name?report.name.value:""}
                        subType ={report.subtype.value}
                        text = {report.text.value}
                        creation_time = {new Date(report.creation_time.value).toDateString()}
                        username ={report.username.value}
                        type={type}
                    />
                </Grid>
            ))}
           </Grid>


     </>
    );

}

export default SuggestionReportBug;