import React, { ChangeEvent, useEffect, useRef, useState } from 'react';
import { Fab, Grid, TextField, Switch, FormControlLabel, Divider, Button, Box   } from '@material-ui/core';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { GoogleMap, LoadScript, Marker, Polyline, InfoWindow } from '@react-google-maps/api';
import { useHistory, Link, Redirect } from 'react-router-dom';
import ReplayIcon from '@material-ui/icons/Replay';
import frontend from '../actions/givers';

interface Event{
  description: "string",
  eventId: "string",
  eventName: "string",
  lat: number,
  lng: number,
  time: number,
}

interface EventEdit{
  description: "string",
  risk:boolean,
  lat: any,
  lng:any,
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      
    },
    paper: {
      width: `100%`,
      height: `100%`,
      overflow: 'scroll'
    },
    fab: {
      position: 'absolute',
      bottom: theme.spacing(7),
      left: theme.spacing(35),
    },
  }),
);

function Map(props) {
  const containerStyle = {
    width: `100%`,
    height: props.height?props.height:`100%`
  };

  const refMap = useRef(null);
  const classes = useStyles();
  const [lat,setLat] = useState(0);
  const [lng,setLong] = useState(0);
  const [center, setCenter] = useState({lat: lat,lng: lng,});
  const [selectedEvent,setSelectedEvent] = useState<Event>();
  const [selectedEditEvent,setSelectedEditEvent] = useState<EventEdit>();
  const [selectedRequest,setSelectedRequest] = useState<any>();
  const [description,setDescription] = useState<string>();
  const [risk,setRisk] = useState<boolean>();

  function onClick(event) {
    if(props.editable){
      const latLng  = event.latLng;
      props.setSelectedMarker(latLng);
      props.setMarkers(props.markers.concat({lat: event.latLng.lat(),lng:event.latLng.lng(),description:"",risk:false}))
    }
  }

  function ResetMarkers(){
    props.setMarkers([]);
  }

useEffect(() => {
  
  navigator.geolocation.getCurrentPosition(function(position) {
    setLat(position.coords.latitude);
    setLong(position.coords.longitude);
    setCenter({lat: position.coords.latitude,lng: position.coords.longitude,});
    console.log(position.coords.longitude+" "+position.coords.latitude)
    props.onLoad(position.coords.latitude,position.coords.longitude);
  });
}, []);
  
  
 /* const handleBoundsChanged = () => {
    const mapCenter = refMap.current.getCenter(); //get map center
    setCenter(mapCenter);
  };
*/
  const defaultcenter = {
    lat: lat,
    lng: lng,
  };

  const onLoad = polyline => {
  };
  
  
  const options = {
    strokeColor: '#FF0000',
    strokeOpacity: 0.8,
    strokeWeight: 2,
    fillColor: '#FF0000',
    fillOpacity: 0.35,
    clickable: false,
    draggable: false,
    editable: false,
    visible: true,
    radius: 30000,
   
    zIndex: 1
  };

  const optionsWalked = {
    strokeColor: '#00FF00',
    strokeOpacity: 0.8,
    strokeWeight: 2,
    fillColor: '#00FF00',
    fillOpacity: 0.35,
    clickable: false,
    draggable: false,
    editable: false,
    visible: true,
    radius: 30000,
   
    zIndex: 1
  };

  function FabButton(){
      if(props.editable){
        return(
          <Fab color="primary" aria-label="add" className={classes.fab} onClick={ResetMarkers} style={{position: "fixed",zIndex:100}}>
            <ReplayIcon />
          </Fab>
        );
    }
  }

  const saveUpdateMarker = (marker,index) =>{
    props.markers.splice(index,1,marker);
//    props.setMarkers(props.markers)
    setDescription("");
    setRisk(false);
  };

  const deleteMarker = (index) =>{
    props.markers.splice(index,1);
    setDescription("");
    setRisk(false);
    setSelectedEditEvent(undefined)
    props.setMarkers(props.markers.concat([]));
  };

  function handleDescription(e:ChangeEvent<HTMLInputElement>){
    setDescription(e.target.value);
  }

  function OnRightClick(event){
    setSelectedRequest({lat: event.latLng.lat(),lng: event.latLng.lng()})
  }

  function AskNearBy(){
    frontend.getMapEventMarkers(selectedRequest.lat,selectedRequest.lng).then((list)=>{
      setSelectedRequest(undefined)
      props.setMapEvents(list)
      console.log(list)
    })
  }

  return (
    <>
    <LoadScript
      googleMapsApiKey="AIzaSyAks4t8_HGOfBmSHJTrJkne61q46p9dU4c"
    >
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={props.center?props.center:center}
        zoom={props.zoom?props.zoom:11}
        ref={refMap}
        onClick={onClick}
        onRightClick={OnRightClick}
      >
        { /* Child components, such as markers, info windows, etc. */ }
        <>
        <Polyline
          onLoad={onLoad}
          path={props.markers}
          options={options}
          
        />
        {props.markers.map((marker,index) => {
          return <Marker onClick={()=>{setSelectedEditEvent(marker);setDescription(marker.description);setRisk(marker.risk)}} position={marker} key={index} icon={index==0?{url:"https://storage.googleapis.com/givers-volunteering.appspot.com/defaultImages/markerEvent.png"}:marker.risk?{url:"https://storage.googleapis.com/givers-volunteering.appspot.com/defaultImages/markerRisk.png"}:""}>{selectedEditEvent==marker && props.editable && <InfoWindow position={selectedEditEvent} onCloseClick={()=>{setSelectedEditEvent(undefined)}}> 
          <div>
            <h2>
              {"Editar Ponto"}
            </h2>
            <Grid container>
              <Grid item xl={12}>
                <TextField
                  variant="outlined"
                  id="desc"
                  label="Descrio"
                  multiline
                  rows={2}
                  value={description}
                  onChange={handleDescription}
                  fullWidth
                />
              </Grid>
              <Divider/>
              <Grid item xl={6} >
                <FormControlLabel label="Zona de Risco" onClick={()=>{setRisk(!risk)}} checked={risk} labelPlacement="start" control={<Switch color="primary"/>}  />
              </Grid>
              <Grid item xl={6} >
                <Box display="flex" flexDirection="row" justifyContent="flex-end" alignItems="center" height="100%" width="100%">
                  <Button style={{marginRight:"2%"}} onClick={()=>{deleteMarker(index)}} color="secondary" size="small" variant="contained">Apagar</Button>
                  <Button onClick={()=>{saveUpdateMarker({lat: marker.lat,lng: marker.lng, description:description,risk:risk},index)}} color="primary" size="small" variant="contained">Salvar</Button>
                </Box>
              </Grid>
            </Grid>
          </div></InfoWindow>}{selectedEditEvent==marker && !props.editable && <InfoWindow position={selectedEvent} onCloseClick={()=>{setSelectedEvent(undefined); setDescription("");setRisk(false)}}> 
          <div>
            <h2>
              {"Marcador "+(index+1) + (marker.risk?" ":"") }
            </h2>
            <p>{marker.description} </p> 
          </div></InfoWindow>}
</Marker>
        })}
        {props.mapEvents.map((marker,index) => {
          return <Marker onClick={()=>{setSelectedEvent(marker)}} position={marker} key={index} icon={{url:"https://media.discordapp.net/attachments/817440806054789181/864095750051725322/Untitled-2.png?width=30&height=48"}}/>           
        })}

        {selectedEvent && <InfoWindow position={selectedEvent} onCloseClick={()=>{setSelectedEvent(undefined); setDescription("");setRisk(false)}}> 
          <div>
            <h2>
              {selectedEvent.eventName}
            </h2>
            <p>{selectedEvent.description} <Link to={'event/'+selectedEvent.eventId}>Saber Mais</Link></p>
          </div></InfoWindow>}

        {selectedRequest && <InfoWindow position={selectedRequest} onCloseClick={()=>{setSelectedRequest(undefined)}}> 
          <div>
           <Button color="primary" variant="contained" onClick={AskNearBy}>Pedir Eventos Prximos</Button>
          </div></InfoWindow>}

        {props.walkedMarkers && <Polyline
          onLoad={onLoad}
          path={props.walkedMarkers}
          options={optionsWalked}
          
        />}

        {props.walkedMarkers && props.walkedMarkers.map((marker,index)=>{
          return <Marker position={marker} key={index} />
        })}

        </>
      </GoogleMap>
    </LoadScript>
    {FabButton()}
    </>
  )
}


export default Map;