import React, { useEffect } from 'react';
import { Redirect } from 'react-router';
import frontend from './actions/givers';
import { Grid, Card, Box, Paper, List } from '@material-ui/core';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import UserEventCard from './components/Card/UserEventCard';
import SavedEventCard from './components/Card/SavedEventCard';
import { ContactSupportOutlined, Event } from '@material-ui/icons';
import CSS from 'csstype';
import { CSSProperties } from '@material-ui/core/styles/withStyles';
import InstitutionFutureEvent from './components/Card/InstitutionFutureEvent';

interface Event {
    "address": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "capacity": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "date_end": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "date_start": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": number,
      "valueType": string
    },
    "description": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "photo": {
      "excludeFromIndexes": false,
      "meaning": number,
      "value": string,
      "valueType": string
  },
    "institution": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "interests": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "name": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
    },
    "participants": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": [],
      "valueType": string
    },
    "points_rewarded": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": boolean,
      "valueType": string
  },
    "start_code": {
      "excludeFromIndexes": boolean,
      "meaning": number,
      "value": string,
      "valueType": string
  }

}

interface Institution{
  "name": {
    "excludeFromIndexes": boolean,
    "meaning": number,
    "value": string,
    "valueType": string
  }
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      height: '100%',
      padding: theme.spacing(1),
      textAlign: 'center',
      color: 'theme.palette.text.secondary',
    },
  }),
);


function InstitutionEvents() {
  const classes = useStyles();
  
  const [userFutureEvents,setUserFutureEvents] = React.useState<Event[]>([]);
  const [userPastEvents,setUserPastEvents] = React.useState<Event[]>([]);
  const [userOnGoingEvents,setUserOnGoingEvents] = React.useState<Event[]>([]);
  const [hasOnGoingEvents, setHasOnGoingEvents] = React.useState(false)
  const [hasPastEvents, setHasPastEvents] = React.useState(false)
  const [hasFutureEvents, setHasFutureEvents] = React.useState(false)
  const [institution, setInstitution] = React.useState<string>("");

  const [cursor,setCursor] = React.useState("");

  const titleStyle: CSSProperties = {
    color: 'white',
    fontWeight: 'bold',
    fontSize: '20px',
    padding: '3px 10px',
    backgroundColor: '#84CEEB',
    borderRadius: '5px',
    textAlign : 'left'

  };
  
  const noneStyle: CSSProperties = {
    textAlign: 'left',
    fontSize: '15px',
    padding: '3px 10px',
    color: '#4D6D9A',
    borderRadius: '5px'
  }
  useEffect(()=>{
  },[]);
  
  
  useEffect(()=>{
    
    frontend.getOwnInstitution().then( (inst) => {
    setInstitution( inst.name.value);    
   
    frontend.getInstitutionEvents(inst.name.value, "1").then((events)=>{
      if(events.length==0){
        setHasFutureEvents(false);  

      } else{
         setUserFutureEvents(events);
         setHasFutureEvents(true);

      }
    
    })
    
    frontend.getInstitutionEvents(inst.name.value, "-1").then((events)=>{
      if(events.length==0){
        setHasPastEvents(false)

      } else{
        setUserPastEvents(events);
        setHasPastEvents(true);

      }
    })


    frontend.getInstitutionEvents(inst.name.value,"2").then((events)=>{
      if(events.length==0){
        setHasOnGoingEvents(false)
      }
      else{

        setUserOnGoingEvents(events);
        setHasOnGoingEvents(true)
      }

    })
    
  }).catch(()=>{});
  },[]);


  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }

 

  return (
    <div className={classes.root}>
      <Grid container spacing={3} style={{height:'89vh', display: 'inline-block', minWidth:'200'}}>
      <h3 style={titleStyle}>Eventos a Decorrer</h3>
        <Grid container item    xs={12} sm={12} spacing={3} >
          {!hasOnGoingEvents && <p style =  {noneStyle}>No tem nenhum evento a decorrer</p>}
          {userOnGoingEvents.map((event, ix) => {
            return(
            <Grid item>

              <UserEventCard style={{minWidth:'300px'}}
                key={ix}
                name={event.name.value}
                description={event.description.value}
                date_start={event.date_start.value}
                address={event.address.value}
                photo = {event.photo.value}
                code= {event.start_code.value}
                />
            </Grid>);
            }
          )}
        </Grid>
        <h3 style={titleStyle}>Prximos Eventos</h3>
        <Grid container  spacing={3} >
        {!hasFutureEvents && <p style={noneStyle}> No tem nenhum evento planeado para o futuro...</p>}
          { userFutureEvents.map((event, ix) => (
          <Grid item >
          <InstitutionFutureEvent style={{minWidth:'300px'}}
            key={ix}
            name={event.name.value}
            description={event.description.value}
            date_start={event.date_start.value}
            address={event.address.value}
            photo = {event.photo.value}
            code= {event.start_code.value}
          />
          </Grid>))}
        </Grid>
       
        <h3 style={titleStyle} >Eventos Passados</h3>
        {!hasPastEvents && <p style={noneStyle}>Ainda no organizou nenhum evento!</p>}
        <Grid container spacing={3}>
        {hasPastEvents && userPastEvents.map((event, ix) => (
          <Grid item>
            <UserEventCard style={{minWidth:'300px'}}
              key={ix}
              name={event.name.value}
              description={event.description.value}
              date_start={event.date_start.value}
              address={event.address.value}
              photo = {event.photo.value}

            />
           </Grid>))}

        </Grid>
        
      </Grid>
    </div>


  );
}

export default InstitutionEvents;
