import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import frontend from '../../actions/givers';
import { useSnackbar } from 'notistack';

export default function AlertDialog(props) {
  const [open, setOpen] = React.useState(false);
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  const handleClickOpen = () => {
    setOpen(true);
  };

const leaveEvent = () => {
    frontend.leaveEventUser(props.name).then((response) => {
      handleClose();
      enqueueSnackbar(response, { variant: 'success' });
    }).catch((error)=>{
      enqueueSnackbar(error.message, { variant: 'error' });
    });
  }

  const handleClose = () => {
    setOpen(false);
  };

  


  return (
    <div>
      <Button size="small" style={{backgroundColor: '#84CEEB', color: 'white'}} onClick={handleClickOpen} >
          Sair do Evento
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Tem a certeza que quer cancelar a sua inscrio do evento?"}</DialogTitle>
        
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={leaveEvent} color="primary" autoFocus>
            Sim
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
