import { Box, Button, Card, FormControlLabel, Grid, Switch, TextField, IconButton } from '@material-ui/core';
import React, { ChangeEvent, useState } from 'react'
import Tooltip from '@material-ui/core/Tooltip';
import InfoIcon from '@material-ui/icons/Info';
import frontend from '../../actions/giversBO';
import { Redirect } from 'react-router';
import { useSnackbar } from 'notistack';

function RBAC(){
    const [method,setMethod] = useState("")
    const [user,setUser] = useState(false)
    const [instOwner,setInstOwner] = useState(false)
    const [groupOwner,setGroupOwner] = useState(false)
    const [bo,setBo] = useState(false)
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    function handleMethod(e:ChangeEvent<HTMLInputElement>){
        setMethod(e.target.value);
    }

    function AddRule(){
        frontend.AddRBAC(method,user,instOwner,groupOwner,bo).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    function EditRule(){
        frontend.EditRBAC(method,user,instOwner,groupOwner,bo).then((text)=>{
            enqueueSnackbar(text, { variant: 'success' });
        }).catch((error)=>{
            enqueueSnackbar(error.message, { variant: 'error' });
        })
    }

    if(!frontend.Authenticated()){
        return <Redirect to="/"/>
    }

    if(!frontend.isSuper() && !frontend.isBack()){
        
        return <Redirect to="/app/dashboard"/>
    }


    return(
        <Box height={window.innerHeight-155} display="flex" alignItems="center" justifyContent="center">
        <Card style={{width:"30%"}}>

            <Grid container justifyContent="space-around" alignItems="center" spacing={2} style={{padding:15}}>
                <Grid item xl={12}>
                    <TextField
                        id="methodName"
                        label="Mtodo"
                        variant="outlined"
                        value={method}
                        onChange={handleMethod}
                        fullWidth
                        />
                </Grid>

                <Grid item sm={12} xl={6}>
                    <FormControlLabel
                        control={<Switch color="primary" checked={user} onChange={()=>{setUser(!user)}} name="User" />}
                        label="USER"
                        />
                </Grid>

                <Grid item sm={12} xl={6}>
                    <FormControlLabel
                        control={<Switch color="primary" checked={instOwner} onChange={()=>{setInstOwner(!instOwner)}} name="InstOwner" />}
                        label="INST_OWNER"
                        />
                </Grid>

                <Grid item sm={12} xl={6}>
                    <FormControlLabel
                        control={<Switch color="primary" checked={groupOwner} onChange={()=>{setGroupOwner(!groupOwner)}} name="GroupOwner" />}
                        label="GROUP_OWNER"
                        />
                </Grid>

                <Grid item sm={12} xl={6}>
                    <FormControlLabel
                        control={<Switch color="primary" checked={bo} onChange={()=>{setBo(!bo)}} name="BackOffice" />}
                        label="BO"
                        />
                </Grid>

                <Grid container item xl={12} direction="row">
                            <Grid item xl={1}>
                                <Tooltip title={"Indique o nome do mtodo que quer adicionar ou editar, e as permisses para cada um dos roles. O role BO s ser aceite se voc for um role SU."}>
                                    <IconButton aria-label="">
                                        <InfoIcon />
                                    </IconButton>
                                </Tooltip>
                            </Grid >
                            <Grid item xl={11}>
                                <Box height="100%" width="100%" display="flex" alignItems="center" justifyContent="flex-end" >
                                    <Button  style={{marginRight:5}} variant="contained" color="primary" onClick={EditRule}>Editar</Button>
                                    <Button  variant="contained" color="primary" onClick={AddRule}>Adicionar</Button>
                                </Box>
                            </Grid>
                        </Grid>
                </Grid>
            </Card>
        </Box>
    );

}

export default RBAC;